import { Reducer } from 'redux'
import produce from 'immer'
import {
    InClassReducerState,
    InClassAction,
    SET_CALENDAR_EVENT,
    SET_URL,
    SET_FEEDBACK_MODAL,
    TOGGLE_ENABLED,
    GROUP_COMPLETED,
    GROUP_INCOMPLETE,
    TOGGLE_ENTRY,
    REPLACE_STATE,
} from './inClassTypes'
import { Timesheet, TimesheetEntry, TimesheetEntryGroup } from '../types/groupTypes'

function isGroupEntry(entry: TimesheetEntry): entry is TimesheetEntryGroup {
    return entry.type === 'group'
}

export const INITIAL_STATE: InClassReducerState = {
    enabled: false,
    feedbackModalOpen: false,
    version: 0,
    startTime: 0,
    totalDuration: 0,
    entryGroupCounters: [],
    entryGroupDurations: [],
    entryCheckboxes: [],
    groupCheckboxes: [],
}

export const inClassReducer: Reducer<InClassReducerState, InClassAction> = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case SET_CALENDAR_EVENT:
            if (state.calendarEvent === action.payload) return state
            return {
                ...state,
                version: state.version + 1,
                calendarEvent: action.payload || null,
                sessionId: action.payload?._id,
                groupId: action.payload?.group_id || 'undefined',
            }

        case SET_URL:
            if (state.url === action.payload) return state
            return {
                ...state,
                version: state.version + 1,
                url: action.payload,
            }

        case SET_FEEDBACK_MODAL:
            if (state.feedbackModalOpen === action.payload) return state
            return {
                ...state,
                version: state.version + 1,
                feedbackModalOpen: action.payload,
            }

        case TOGGLE_ENABLED:
            if (state.enabled) {
                return {
                    ...state,
                    version: state.version + 1,
                    enabled: false,
                    feedbackModalOpen: true,
                    startTime: Date.now(),
                    totalDuration: 0,
                    entryGroupDurations: [],
                    entryGroupCounters: [],
                    entryCheckboxes: [],
                    groupCheckboxes: [],
                }
            } else {
                const { timesheet } = action.payload as { timesheet: Timesheet }
                if (!timesheet) return state

                const counters = timesheet.content.entries.map(entry =>
                    isGroupEntry(entry)
                        ? Math.max(entry.isActive ? 1 : 0, entry.content.entries.filter(e => e.isActive).length)
                        : 0,
                )

                const durations = timesheet.content.entries.map(e => +e.duration)

                const totalDuration = timesheet.content.entries.reduce((acc, entry) => acc + +entry.duration, 0)

                return {
                    ...state,
                    version: state.version + 1,
                    enabled: true,
                    startTime: Date.now(),
                    totalDuration: totalDuration,
                    entryGroupCounters: [...counters],
                    entryGroupDurations: durations,
                    entryCheckboxes: timesheet.content.entries.map(e =>
                        isGroupEntry(e)
                            ? e.content.entries.map(e => ({
                                  value: !e.isActive,
                                  duration: 0,
                                  active: e.isActive,
                              }))
                            : [],
                    ),
                    groupCheckboxes: timesheet.content.entries.map(e => (isGroupEntry(e) ? !e.isActive : false)),
                }
            }

        case GROUP_COMPLETED:
            return produce(state, draft => {
                const index = action.payload
                draft.version = state.version + 1
                if (draft.entryCheckboxes[index]) {
                    const isAllInactive = draft.entryCheckboxes[index].every(checkbox => !checkbox.active)
                    draft.entryCheckboxes[index] = draft.entryCheckboxes[index].map(checkbox => {
                        return {
                            ...checkbox,
                            value: true,
                            duration:
                                checkbox.active || state.entryCheckboxes[index].length === 1
                                    ? draft.entryGroupDurations[index] / draft.entryGroupCounters[index]
                                    : isAllInactive
                                    ? draft.entryGroupDurations[index] / state.entryCheckboxes[index].length
                                    : 0,
                            active: true,
                        }
                    })
                }
                if (!draft.groupCheckboxes) {
                    draft.groupCheckboxes = draft.entryCheckboxes.map(() => false)
                }
                draft.groupCheckboxes[index] = true
            })

        case GROUP_INCOMPLETE:
            return produce(state, draft => {
                const index = action.payload
                draft.version = state.version + 1
                if (draft.entryCheckboxes[index]) {
                    draft.entryCheckboxes[index] = draft.entryCheckboxes[index].map(checkbox => ({
                        ...checkbox,
                        value: false,
                        duration:
                            checkbox.active || draft.entryCheckboxes[index].length === 1
                                ? draft.entryGroupDurations[index] / draft.entryGroupCounters[index]
                                : 0,
                    }))
                }
                if (!draft.groupCheckboxes) {
                    draft.groupCheckboxes = state.entryCheckboxes.map(() => false)
                }
                draft.groupCheckboxes[index] = false
            })

        case TOGGLE_ENTRY: {
            return produce(state, draft => {
                const { index, entryIndex, value } = action.payload
                if (draft.entryCheckboxes[index]) {
                    const active = state.entryCheckboxes[index][entryIndex].active
                    draft.version = state.version + 1
                    draft.entryCheckboxes[index][entryIndex] = {
                        value,
                        duration: active ? draft.entryGroupDurations[index] / draft.entryGroupCounters[index] : 0,
                        active,
                    }
                }
                if (!draft.groupCheckboxes) {
                    draft.groupCheckboxes = draft.entryCheckboxes.map(() => false)
                }
                draft.groupCheckboxes[index] =
                    draft.entryCheckboxes[index].filter(checkbox => checkbox.value).length ===
                    draft.entryGroupCounters[index]
            })
        }

        case REPLACE_STATE: {
            return {
                ...action.payload.value,
                version: state.version,
            }
        }

        default:
            return state
    }
}
