import React from 'react'
import { Box, Stack } from 'components/layout'
import { TimesheetChallengeGroupEntry } from '../../../types/groupTypes'
import { Button } from 'components/Button'
import styled from 'styled-components/macro'
import { useTranslate } from 'lib/i18n/useTranslate'
import { HTMLText } from './HTMLText'
import { Text } from 'components/Text'
import { api } from 'lib/api'
import { Dialog, DialogActions, DialogContent, DialogPrimaryButton, DialogTitle } from 'components/dialog'
import { useProcessSecretCodeRedeemResponse } from '../../../../secret-code'

const HorizontalLeftAlignedStack = styled(Box)`
    display: flex;
    align-items: center;
    gap: ${props => props.theme.spacing(2)};
    justify-content: flex-start;
`

const generateSecretCode = async (id: string) => {
    const challengeSet = await api.groups.getChallengeSet(id)

    const response = await api.secretCodes.generate({
        kind: 'event-participation',
        target: challengeSet.events[0],
        reason: 'generatedFromTimesheet',
    })

    return response.code
}

export const ChallengeGroupEntry: React.FC<{ entry: TimesheetChallengeGroupEntry }> = ({ entry }) => {
    const t = useTranslate()
    const [state, setState] = React.useState('idle')
    const [secretCodeState, setSecretCodeState] = React.useState('idle')
    const [code, setCode] = React.useState('')
    const processSecretCodeRedeemResponse = useProcessSecretCodeRedeemResponse()

    const handleShowCode = async () => {
        if (!code) {
            setState('progress')
            setCode(await generateSecretCode(entry.content.challenge_set_id))
        }
        setState('modal')
    }

    const handleRedeem = async () => {
        if (secretCodeState !== 'progress') {
            setSecretCodeState('progress')
            try {
                const { data } = await api.secretCodes.redeem(code)
                if (data.success) processSecretCodeRedeemResponse(data, () => setState('idle'))
                setSecretCodeState('idle')
            } catch (e) {
                console.error(e)
                setSecretCodeState('error')
            }
        }
    }

    return (
        <>
            <Stack>
                <HTMLText text={entry.content.details} />
                <HorizontalLeftAlignedStack>
                    <Button disabled={state !== 'idle'} variant="contained" onClick={() => handleShowCode()}>
                        {state !== 'progress' &&
                            t('timesheet.showChallengeSetCode', { defaultValue: 'Show Secret Code' })}
                        {state === 'progress' &&
                            t('timesheet.generatingChallengeSetCode', { defaultValue: 'Generating code...' })}
                    </Button>
                </HorizontalLeftAlignedStack>
            </Stack>
            <Dialog fullWidth open={state === 'modal'} onClose={() => setState('idle')}>
                <DialogTitle>{t('secretCode::label')}</DialogTitle>
                <DialogContent>
                    <Text variant="h1">{code}</Text>
                </DialogContent>
                <DialogActions>
                    <DialogPrimaryButton
                        isLoading={secretCodeState === 'progress'}
                        disabled={secretCodeState === 'error'}
                        onClick={handleRedeem}
                    >
                        {t('redeemSecretCode')}
                    </DialogPrimaryButton>
                </DialogActions>
            </Dialog>
        </>
    )
}
