import { useState, useCallback, useEffect } from 'react'

export function useIsElementFullyHidden(headerHeight?: number) {
    const limit = headerHeight || 80

    const [isFullyHidden, setIsFullyHidden] = useState(false)

    const onScroll = useCallback(() => {
        setIsFullyHidden(typeof window.scrollY === 'number' && (window.scrollY >= limit))
    }, [limit])

    useEffect(() => {
        window.addEventListener('scroll', onScroll)
        onScroll()
        return () => window.removeEventListener('scroll', onScroll)
    }, [onScroll])

    return isFullyHidden
}
