import React from 'react'
import { TimesheetEntry } from '../../types/groupTypes'
import { EntryGroup, StatusEntry } from './timesheet'

export const TimesheetEntryView: React.FC<{ entry: TimesheetEntry; index: number; sessionId?: string }> = ({
    entry,
    index,
    sessionId,
}) => {
    switch (entry.type) {
        case 'group':
            return <EntryGroup entry={entry} index={index} sessionId={sessionId} />
        case 'status':
            return <StatusEntry entry={entry} />
        default:
            return <pre>{JSON.stringify(entry, null, 2)}</pre>
    }
}
