import React from 'react'
import styled from 'styled-components/macro'
import { CardContent } from '@material-ui/core'
import { Box } from 'components/layout'
import { Text } from 'components/Text'
import { TimesheetStatusEntry } from '../../../types/groupTypes'
import ComputerOff from '../../../media/Computer Parts Monitor.png'
import ComputerOn from '../../../media/Computer Parts Monitor Blue Death.png'
import Break from '../../../media/Sleepy Owl Idle Sleepy.png'
import { useTranslate } from 'lib/i18n/useTranslate'

const HorizontalLeftAlignedStack = styled(Box)`
    display: flex;
    align-items: center;
    gap: ${props => props.theme.spacing(1)};
    justify-content: flex-start;
`

export const StudentComputerContent: React.FC<{ content: 'on' | 'off' }> = ({ content }) => {
    const t = useTranslate()

    return (
        <>
            <img
                src={content === 'on' ? ComputerOn : ComputerOff}
                style={{ width: '4rem', marginRight: '1rem' }}
                alt="Computer"
            />
            <Text>
                {t(`timesheet.student-computer.${content}`, {
                    defaultValue: `Students should turn ${content} the computer.`,
                })}
            </Text>
        </>
    )
}

export const BreakContent: React.FC<{ duration: number }> = ({ duration }) => {
    const t = useTranslate()

    return (
        <>
            <img
                src={Break}
                style={{ width: '4rem', marginTop: '-1rem', marginBottom: '-1.5rem', marginRight: '1rem' }}
                alt="Sleepy Owl"
            />
            {!!duration && (
                <Text>
                    {t('timesheet.break.duration', {
                        duration,
                        defaultValue: 'Take a break for ' + duration + ' minutes',
                    })}
                </Text>
            )}
            {!duration && <Text>{t('timesheet.break.on', { defaultValue: 'Take a break!' })}</Text>}
        </>
    )
}

export const StatusEntry: React.FC<{ entry: TimesheetStatusEntry }> = ({ entry }) => {
    return (
        <CardContent style={{ padding: '0.5rem 0.7rem' }}>
            <HorizontalLeftAlignedStack>
                {entry.subtype === 'student-computer' && <StudentComputerContent content={entry.content} />}
                {entry.subtype === 'break' && <BreakContent duration={entry.duration} />}
            </HorizontalLeftAlignedStack>
        </CardContent>
    )
}
