import React, { useEffect, useState } from 'react'
import styled from 'styled-components/macro'
import { CardContent, Theme, useMediaQuery, useTheme } from '@material-ui/core'
import { Box, Stack } from 'components/layout'
import { Text } from 'components/Text'
import { Tutorial } from '../../../types/groupTypes'
import { ActionCard } from 'components/action-card'
import ReactPlayer from 'react-player'
import { Dialog, DialogContent, DialogTitle } from 'components/dialog'
import { useVimeoThumbnailQuery } from '../../../groupQueries'
import { Card } from 'components/card'

const HorizontalLeftAlignedStack = styled(Box)`
    display: flex;
    align-items: center;
    gap: ${props => props.theme.spacing(1)};
    justify-content: flex-start;
`

const VimeoActionCard: React.FC<{ entry: Tutorial; active?: boolean; onClick: () => void }> = ({
    entry,
    onClick,
    active,
}) => {
    const { data: thumbnail } = useVimeoThumbnailQuery(entry.url)
    const theme = useTheme()

    return (
        <ActionCard
            type="button"
            onClick={e => {
                e.stopPropagation()
                e.preventDefault()
                onClick()
                return false
            }}
            style={{ border: `.1rem solid ${active ? theme.palette.primary.main : 'transparent'}` }}
        >
            <CardContent style={{ padding: '0.5rem 0.7rem' }}>
                <HorizontalLeftAlignedStack>
                    <img
                        src={thumbnail}
                        alt="Thumbnail"
                        style={{ width: '5rem', borderRadius: '.5rem', marginRight: '.3rem' }}
                    />
                    <Text>{entry.title}</Text>
                </HorizontalLeftAlignedStack>
            </CardContent>
        </ActionCard>
    )
}

export const VimeoAttachment: React.FC<{ entry: Tutorial; videoEntries: Tutorial[] }> = ({ entry, videoEntries }) => {
    const [open, setOpen] = useState(false)
    const [activeEntry, setActiveEntry] = useState(entry)
    const isMobile = useMediaQuery<Theme>(theme => theme.breakpoints.down('xs'), { noSsr: true })

    useEffect(() => {
        if (!open) {
            setActiveEntry(entry)
        }
    }, [open, entry])

    const nextEntry = () => {
        const index = videoEntries.indexOf(activeEntry)
        const nextIndex = (index + 1) % videoEntries.length
        if (!nextIndex) {
            setOpen(false)
            return
        }
        setActiveEntry(videoEntries[nextIndex])
    }

    return (
        <>
            <VimeoActionCard entry={entry} onClick={() => setOpen(true)} />
            <Dialog fullWidth fullHeight maxWidth="md" open={open} onClose={() => setOpen(false)}>
                <DialogTitle>{activeEntry.title}</DialogTitle>
                <DialogContent>
                    <Box
                        display="flex"
                        flexDirection={isMobile ? 'column' : 'row'}
                        style={{
                            gap: '1rem',
                            height: isMobile ? undefined : '100%',
                        }}
                    >
                        <Card style={{ flex: isMobile ? 'auto' : 3 }}>
                            <CardContent>
                                <ReactPlayer
                                    style={{ borderRadius: '.5rem' }}
                                    url={activeEntry.url}
                                    config={{
                                        vimeo: {
                                            playerOptions: {
                                                controls: true,
                                                autoplay: true,
                                                byline: false,
                                                title: false,
                                            },
                                        },
                                    }}
                                    playing
                                    controls
                                    width="100%"
                                    height={isMobile ? 'auto' : undefined}
                                    onEnded={nextEntry}
                                />
                            </CardContent>
                        </Card>
                        <Stack style={{ height: '100%', flex: 1 }} spacing={1}>
                            {videoEntries.map((video, index) => (
                                <VimeoActionCard
                                    key={index}
                                    entry={video}
                                    active={activeEntry.index === video.index}
                                    onClick={() => setActiveEntry(video)}
                                />
                            ))}
                        </Stack>
                    </Box>
                </DialogContent>
            </Dialog>
        </>
    )
}
