export function secondsToHHMMSS(seconds: number) {
    const _hours = Math.floor(seconds / 3600).toLocaleString('en-US', { minimumIntegerDigits: 2, useGrouping: false })
    const s = seconds % 3600
    const _minutes = Math.floor(s / 60).toLocaleString('en-US', { minimumIntegerDigits: 2, useGrouping: false })
    const _seconds = (s % 60).toLocaleString('en-US', { minimumIntegerDigits: 2, useGrouping: false })
    const time = _hours + ':' + _minutes + ':' + _seconds
    return time
}
