export interface InClassContextValue {
    state: InClassReducerState
    dispatch: ReturnType<typeof useDispatch>
}

export const InClassContext = React.createContext<InClassContextValue>({
    state: {
        enabled: false,
        feedbackModalOpen: false,
        startTime: 0,
        version: 0,
        totalDuration: 0,
        entryGroupCounters: [],
        entryGroupDurations: [],
        entryCheckboxes: [],
        groupCheckboxes: [],
    },
    dispatch: () => ({} as any),
})

export const useInClassContext = (index: number, entryIndex?: number) => {
    const context = useContext(InClassContext)
    const location = useLocation()
    if (!context) {
        throw new Error('useInClassContext must be used within a InClassContextProvider')
    }

    const isInClassMode = context.state.enabled && context.state.url === location.pathname
    const isGroupCompleted =
        (isInClassMode &&
            context.state.entryCheckboxes[index]?.some(e => e.active) &&
            context.state.entryCheckboxes[index]?.every(
                entry => entry.value || (context.state.entryCheckboxes[index].length > 1 && !entry.active),
            )) ??
        false
    const groupChecked = context.state.groupCheckboxes?.[index] ?? false
    const entryChecked =
        typeof entryIndex !== 'undefined' ? context.state.entryCheckboxes?.[index]?.[entryIndex]?.value ?? false : false
    const isMakeUp = context.state.calendarEvent?.type === 'makeup'

    return useMemo(() => {
        return {
            isInClassMode: isInClassMode && !isMakeUp,
            isGroupCompleted: groupChecked || isGroupCompleted,
            isEntryCompleted: entryChecked || isGroupCompleted,
            isMakeUp,
            hasMultipleEntries: context.state.entryGroupCounters[index] > 1,

            toggleGroup: () => {
                if (groupChecked) {
                    context.dispatch(groupIncomplete(index))
                } else {
                    context.dispatch(groupCompleted(index))
                }
            },

            toggleEntry: () => {
                if (entryChecked) {
                    context.dispatch(toggleEntry(index, entryIndex!, false))
                } else {
                    context.dispatch(toggleEntry(index, entryIndex!, true))
                }
            },
        }
    }, [isInClassMode, isGroupCompleted, isMakeUp, groupChecked, entryChecked, context, index, entryIndex])
}

const useTimeCalculation = () => {
    const { state } = useContext(InClassContext)
    const [elapsedTime, setElapsedTime] = useState(state.enabled ? Date.now() - state.startTime : 0)

    useEffect(() => {
        if (!state.enabled) return
        const timer = setInterval(() => {
            setElapsedTime(Date.now() - state.startTime)
        }, 1000)
        return () => clearInterval(timer)
    }, [state.startTime, state.enabled])

    const estimatedElapsedTime = state.entryCheckboxes.reduce((total, group, index) => {
        const groupSum = group.reduce((groupTotal, entry) => {
                return groupTotal + (entry.value ? entry.duration : 0)
            }, 0)
        if (groupSum > state.entryGroupDurations[index]) {
            return total + state.entryGroupDurations[index]
        }
        return total + groupSum
    }, 0)
    
    const estimatedTime = state.totalDuration - estimatedElapsedTime
    const elapsedSeconds = Math.round(elapsedTime / 1000)
    const elapsedMinutes = Math.round(elapsedTime / 60000)
    const isElapsedInRange =
        elapsedMinutes >= Math.round(estimatedTime * 0.1) && elapsedMinutes <= Math.round(estimatedTime * 0.9)
    const isEndingSoon =
        elapsedMinutes > Math.round(state.totalDuration * 0.9) ||
        estimatedElapsedTime > Math.round(state.totalDuration * 0.9)

    return {
        elapsedTime,
        elapsedSeconds,
        elapsedMinutes,
        estimatedElapsedTime,
        estimatedTime,
        isElapsedInRange,
        isEndingSoon,
    }
}

const ElapsedTimeFab: React.FC<{ state: InClassReducerState; dispatch: ReturnType<typeof useDispatch> }> = ({
    state,
    dispatch,
}) => {
    const t = useTranslate()
    const [elapsedSeconds, setElapsedSeconds] = useState(0)

    useEffect(() => {
        if (!state.enabled) return
        const timer = setInterval(() => {
            const elapsedTime = Date.now() - state.startTime
            setElapsedSeconds(Math.round(elapsedTime / 1000))
        }, 1000)
        return () => clearInterval(timer)
    }, [state.startTime, state.enabled])

    const location = useLocation()
    if (state.url === location.pathname || !state.enabled) return null

    return (
        <Link to={state.url || ''}>
            <Tooltip title={t('classroom::backToSession')} placement="left">
                <InClassFab color="primary" size="large">
                    {`${Math.floor(elapsedSeconds / 60)
                        .toString()
                        .padStart(2, '0')}:${(elapsedSeconds % 60).toString().padStart(2, '0')}`}
                </InClassFab>
            </Tooltip>
        </Link>
    )
}

export const InClassModeContextProvider: React.FC = ({ children }) => {
    const state = useSelector(state => state.inClass)
    const dispatch = useDispatch()
    const value = useMemo(() => ({ state, dispatch }), [state, dispatch])

    return (
        <InClassContext.Provider value={value}>
            {children}
            <ElapsedTimeFab state={state} dispatch={dispatch} />
        </InClassContext.Provider>
    )
}

export interface InClassModeCardProps {
    calendarEvent: CalendarEntryEventData
    timesheet: Timesheet
    timesheetId: string
    lessonType: string
    lessonIndex: number
    courseType: string
    numberOfStudents: number
    horizontalLayout?: boolean
}

export const InClassModeCard: React.FC<InClassModeCardProps> = ({
    horizontalLayout,
    timesheet,
    timesheetId,
    calendarEvent,
    ...feedback
}) => {
    const t = useTranslate()
    const { state, dispatch } = useContext(InClassContext)
    const StackComponent = horizontalLayout ? HorizontalStack : Stack
    const isMobile = useMediaQuery<Theme>(theme => theme.breakpoints.down('xs'), { noSsr: true })
    const calendarEvents = useMemo(() => [calendarEvent], [calendarEvent])

    const {
        elapsedTime,
        elapsedSeconds,
        elapsedMinutes,
        estimatedElapsedTime,
        estimatedTime,
        isElapsedInRange,
        isEndingSoon,
    } = useTimeCalculation()

    // Set the calendar event when the component mounts
    useEffect(() => {
        if (calendarEvent) {
            dispatch(setCalendarEvent(calendarEvent))
        }
    }, [dispatch, calendarEvent])

    const location = useLocation()

    /*const status = useMemo(() => {
        if(elapsedMinutes >= estimatedElapsedTime-7.5 && elapsedMinutes <= estimatedElapsedTime+7.5) {
            return {
                icon: <StatusIconGreen color="#4CAF50" />,
                label: "classroom::timeStatus::onTime"
            }
        }
        else if(elapsedMinutes >= estimatedElapsedTime-15 && elapsedMinutes <= estimatedElapsedTime+15) {
            return {
                icon: <StatusIconYellow color="#dfa13b" />,
                label: (elapsedMinutes > estimatedElapsedTime) ? "classroom::timeStatus::slow" : "classroom::timeStatus::fast"
            }
        }
        else {
            return {
                icon: <StatusIconRed color="#F44336" />,
                label: (elapsedMinutes > estimatedElapsedTime) ? "classroom::timeStatus::verySlow" : "classroom::timeStatus::veryFast"
            }
        }
    }, [elapsedMinutes, estimatedElapsedTime])*/

    if (horizontalLayout && !elapsedTime && !isMobile) {
        return null
    }

    const isMakeUp = calendarEvent.type === 'makeup'
    const onlineMeetingUrl =
        calendarEvent &&
        calendarEvent.online &&
        (calendarEvent as any)['onlineMeeting.hidden'] !== true &&
        (calendarEvent as any)['onlineMeeting.url']
    const isPast = dayjs().isAfter(calendarEvent.endTime)
    
    let contentPart = (
        <StackComponent spacing={3} alignItems="center" justifyContent="space-between">
            {state.enabled && (
                <StackComponent spacing={horizontalLayout ? 3 : 1} alignItems="flex-start" justifyContent="flex-end">
                    <Stack spacing={0}>
                        {!horizontalLayout && <SessionHeader calendarEvents={calendarEvents} sessionEnabled={true} />}
                        <Text style={{ marginBottom: horizontalLayout ? '.15rem' : undefined }}>
                            {t('timesheet.elapsedTime')}
                        </Text>
                        <HorizontalStackStart spacing={0} style={{ /*marginLeft: '-.4rem',*/ marginTop: '-.4rem' }}>
                            {/*<Tooltip title={t(status.label)}>
                                <IconButton size="small">
                                    {status.icon}
                                </IconButton>
                            </Tooltip>*/}
                            <Text
                                variant={horizontalLayout ? 'h4' : 'h3'}
                                style={
                                    {
                                        /* marginLeft: '-.2rem'*/
                                    }
                                }
                            >
                                {`${Math.floor(elapsedSeconds / 60)
                                    .toString()
                                    .padStart(2, '0')}:${(elapsedSeconds % 60).toString().padStart(2, '0')}`}
                            </Text>
                        </HorizontalStackStart>
                    </Stack>
                    {!isMakeUp && (
                        <Stack spacing={0}>
                            <Text>{t('timesheet.estimatedRemainingTime')}</Text>
                            <ProgressBar
                                value={estimatedElapsedTime}
                                max={state.totalDuration}
                                ticks={<>{isElapsedInRange && <ProgressBarTick requiredValue={elapsedMinutes} />}</>}
                            >
                                <SmallProgressBarTrack>
                                    <ProgressBarIndicator />
                                    <ProgressBarCenteredText>
                                        {t('common.minutes.mynext', { duration: Math.round(estimatedTime) })}
                                    </ProgressBarCenteredText>
                                </SmallProgressBarTrack>
                            </ProgressBar>
                        </Stack>
                    )}
                </StackComponent>
            )}
            {!state.enabled && (
                <>
                    <SessionHeader calendarEvents={calendarEvents} sessionEnabled={false} />
                </>
            )}
            {!horizontalLayout && (
                <Box display="flex" width="100%" alignItems="center">
                    {(!horizontalLayout || !state.enabled) && (!isPast || state.enabled) && (
                        <Button
                            variant={!state.enabled || isEndingSoon ? 'contained' : 'outlined'}
                            onClick={() => {
                                if (!state.enabled) {
                                    dispatch(setUrl(location.pathname))
                                    dispatch(setCalendarEvent(calendarEvent))
                                }
                                dispatch(toggleEnabled({ timesheet }))
                            }}
                        >
                            {state.enabled
                                ? t(horizontalLayout ? 'timesheet.endButton' : 'timesheet.endClassButton', {
                                      defaultValue: 'End',
                                  })
                                : t('timesheet.startClassButton')}
                        </Button>
                    )}
                    {onlineMeetingUrl && !isPast && !state.enabled && (
                        <Tooltip title={t('meeting::label')}>
                            <Button
                                variant="contained"
                                component="a"
                                href={onlineMeetingUrl}
                                target="_blank"
                                rel="noopener noreferrer"
                                style={{ marginLeft: 'auto' }}
                            >
                                &nbsp;
                                <MeetingIcon style={{ fontSize: 22 }} />
                                &nbsp;
                            </Button>
                        </Tooltip>
                    )}
                </Box>
            )}
        </StackComponent>
    )

    if (horizontalLayout) {
        contentPart = <Box padding={2}>{contentPart}</Box>
    } else {
        contentPart = (
            <Card>
                <CardContent>{contentPart}</CardContent>
            </Card>
        )
    }

    return (
        <>
            {contentPart}
            {!horizontalLayout && (
                <FeedbackModal {...feedback} lessonLength={elapsedTime} progress={0} timesheet={timesheetId} />
            )}
        </>
    )
}

export const EndLessonButtonEntry: React.FC = () => {
    const t = useTranslate()
    const { state, dispatch } = useContext(InClassContext)

    const { isEndingSoon } = useTimeCalculation()

    if (!state.enabled) {
        return null
    }

    return (
        <Card>
            <CardContent style={{ textAlign: 'center' }}>
                <Button
                    variant={!state.enabled || isEndingSoon ? 'contained' : 'outlined'}
                    onClick={() => {
                        dispatch(toggleEnabled({}))
                    }}
                >
                    {t('timesheet.endClassButton')}
                </Button>
            </CardContent>
        </Card>
    )
}

const SessionHeader: React.FC<{ calendarEvents: CalendarEntryEventData[]; sessionEnabled: boolean }> = ({
    calendarEvents,
    sessionEnabled,
}) => {
    const t = useTranslate()
    const { state, dispatch } = useContext(InClassContext)

    if (calendarEvents.length === 1) {
        const g = calendarEvents[0]
        return (
            <Stack spacing={0}>
                <Text
                    style={sessionEnabled ? { fontWeight: 600 } : undefined}
                    color={sessionEnabled ? 'primary' : undefined}
                >
                    {g.group_name || g.sessionType || g.subject_key}
                </Text>
                <HorizontalSubHeader variant="body2" $opacity={1}>
                    {g.type === 'makeup' && (
                        <>
                            <Text variant="body2" color="primary">
                                {t('timesheet.makeup')}
                            </Text>
                            {!state.enabled && (
                                <>
                                    &nbsp;&nbsp;
                                    {g.startTime.format('HH:mm')}-{g.endTime.format('HH:mm')}
                                </>
                            )}
                        </>
                    )}
                    {g.type !== 'makeup' && !state.enabled && (
                        <>
                            {g.startTime.format('HH:mm')}-{g.endTime.format('HH:mm')}
                            &nbsp;&nbsp;
                            {t('classroom::lesson-index', { lessonIndex: g.lesson })}
                        </>
                    )}
                </HorizontalSubHeader>
            </Stack>
        )
    }

    return (
        <Select
            onChange={e => {
                const evt = calendarEvents.find(g => g._id === e.target.value)
                if (evt) {
                    dispatch(setCalendarEvent(evt))
                }
            }}
            value={state.sessionId || ''}
        >
            {calendarEvents.map(g => (
                <Option key={g._id} value={g._id}>
                    <Stack spacing={0}>
                        <Text>{g.group_name || g.sessionType || g.subject_key}</Text>
                        <HorizontalSubHeader variant="body2" $opacity={1}>
                            {g.type === 'makeup' && (
                                <>
                                    <Text variant="body2" color="primary">
                                        {t('timesheet.makeup')}
                                    </Text>
                                    &nbsp;&nbsp;
                                </>
                            )}
                            {g.startTime.format('HH:mm')}-{g.endTime.format('HH:mm')}
                            &nbsp;&nbsp;
                            {t('classroom::lesson-index', { lessonIndex: g.lesson })}
                        </HorizontalSubHeader>
                    </Stack>
                </Option>
            ))}
        </Select>
    )
}

const ConfirmDialog: React.FC<{
    action: () => Promise<void> | void
    ActionWrapper: React.FC<{ action: () => Promise<void> | void }>
    state: InClassReducerState
    dispatch: ReturnType<typeof useDispatch>
}> = ({ action, ActionWrapper, state, dispatch, children }) => {
    const t = useTranslate()
    const history = useHistory()

    const handleBackToSession = () => {
        if (state.url) {
            history.push(state.url)
        }
    }

    const handleLogout = () => {
        dispatch(toggleEnabled({}))
        dispatch(setFeedbackModal(false))
        action()
    }

    return (
        <>
            {state.enabled && (
                <Confirm
                    action={action}
                    dialog={props => (
                        <>
                            <DialogTitle {...props}>{t('general::logout')}</DialogTitle>
                            <DialogContent>
                                <Stack>
                                    {t('classroom::logout-confirm')}
                                    <br />
                                    <LessonText>
                                        {state.calendarEvent?.group_name ||
                                            state.calendarEvent?.sessionType ||
                                            state.calendarEvent?.subject_key}
                                    </LessonText>
                                    <LessonText variant="body2" color="primary">
                                        {t('classroom::lesson-index', { lessonIndex: state.calendarEvent?.lesson })}
                                    </LessonText>
                                </Stack>
                            </DialogContent>
                            <DialogActions>
                                <DialogSecondaryButton onClick={handleLogout}>
                                    {t('classroom::logout-button')}
                                </DialogSecondaryButton>
                                <DialogPrimaryButton
                                    onClick={() => {
                                        handleBackToSession()
                                        props.negativeButtonProps.onClick()
                                    }}
                                >
                                    {t('classroom::backToSession')}
                                </DialogPrimaryButton>
                            </DialogActions>
                        </>
                    )}
                >
                    {confirm => <ActionWrapper action={confirm} />}
                </Confirm>
            )}
            {!state.enabled && <ActionWrapper action={action} />}
        </>
    )
}

const LessonText = styled(Text)`
    display: flex;
    flex-shrink: 0;
    align-items: center;
    font-weight: 600;
    justify-content: center;
    text-align: center;
    color: ${props => props.theme.palette.primary.main};
    line-height: 0.8;
`

