import React, { useEffect, useState } from 'react'
import { Dialog, DialogActions, DialogContent, DialogTitle } from 'components/dialog'
import { Button } from 'components/Button'
import { useTranslate } from 'lib/i18n/useTranslate'
import { Text } from 'components/Text'
import { CheckBox } from 'components/form'
import { Stack } from 'components/layout'
import { useUserActions, useUserState } from 'contexts/UserContext'
import dayjs from 'dayjs'
import { useTermsDateQuery } from '../../groupQueries'
import { Markdown } from 'components/Markdown'


export const ViewTrainerRulesModal: React.FC = () => {
    const [ open, setOpen ] = useState(false);
    const [ progress, setProgress ] = useState(false);
    const [ acceptChecked, setAcceptChecked ] = useState(false);
    const user = useUserState();
    const userActions = useUserActions();
    const { data } = useTermsDateQuery();
    const latestTermsDate = data?.value;
    const accepted = latestTermsDate && user.account.termsAcceptedAt
        && dayjs(user.account.termsAcceptedAt).isAfter(latestTermsDate);
    const t = useTranslate();

    useEffect(() => {
        if(!accepted && latestTermsDate) {
            setOpen(true)
        }
    }, [accepted, latestTermsDate])

    async function acceptRules() {
        setProgress(true);
        await userActions.updateUser({termsAcceptedAt: dayjs().toISOString()})
        setOpen(false);
        setProgress(false)
    }

    return (
        <Stack spacing={2}>
            <Button variant="outlined" fullWidth onClick={() => setOpen(true)}>
                    {t("classroom::trainerRules", { defaultValue: "Trainer Rules" })}
            </Button>
            <Dialog fullWidth fullHeight maxWidth="md"
                    open={open}
                    hideCloseButton={progress || !accepted}
                    onClose={() => {
                        if(accepted) setOpen(false)
                    }}
            >
                <DialogTitle>{t("classroom::trainerRules")}</DialogTitle>
                <DialogContent>
                    <Markdown>{t("classroom::trainerRulesText")}</Markdown>
                </DialogContent>
                <DialogActions>
                    <Text>
                        {t("classroom::overview::rulesLastUpdated", { defaultValue: "Rules last updated at:" })}
                        &nbsp;
                        {dayjs(latestTermsDate).format("YYYY-MM-DD")}
                    </Text>
                    <div style={{ flexGrow: 1 }} />
                    {!accepted && <>
                        <CheckBox label={t("classroom::acceptRules")}
                                  checked={acceptChecked}
                                  disabled={progress}
                                  onChange={e => setAcceptChecked(e.target.checked)}
                        />
                        <Button disabled={!acceptChecked || progress} isLoading={progress}
                                onClick={() => acceptRules()}>
                            {t("general::accept")}
                        </Button>
                    </>}
                    {accepted && <Button onClick={() => setOpen(false)}>{t("general::close")}</Button>}
                </DialogActions>
            </Dialog>
        </Stack>
    )
}