import React from 'react'
import { Box, Stack } from 'components/layout'
import { TimesheetTaskGroupEntry } from '../../../types/groupTypes'
import { Button } from 'components/Button'
import { getIdeUrl } from 'lib/api/helpers'
import styled from 'styled-components/macro'
import { useTranslate } from 'lib/i18n/useTranslate'
import { HTMLText } from './HTMLText'

const HorizontalLeftAlignedStack = styled(Box)`
    display: flex;
    align-items: center;
    gap: ${props => props.theme.spacing(2)};
    justify-content: flex-start;
`

export const TaskGroupEntry: React.FC<{ entry: TimesheetTaskGroupEntry }> = ({ entry }) => {
    const t = useTranslate()
    const handleOpen = (projectId: string) => {
        window.open(getIdeUrl(`/ide/${projectId}`), '_blank')
    }

    return (
        <Stack>
            {entry.content.details && <HTMLText text={entry.content.details} />}
            <HorizontalLeftAlignedStack>
                {entry.content.project_id && (
                    <Button variant="contained" onClick={() => handleOpen(entry.content.project_id)}>
                        {t('timesheet.openProject', { defaultValue: 'Sample Solution' })}
                    </Button>
                )}
                {entry.content.template_id && (
                    <Button variant="outlined" onClick={() => handleOpen(entry.content.template_id)}>
                        {t('timesheet.openTemplate', { defaultValue: 'Template' })}
                    </Button>
                )}
            </HorizontalLeftAlignedStack>
        </Stack>
    )
}
