type PageAsideProps = {
    isMobileMenuOpen: boolean
    onCloseMobileMenu: () => void
    isMobile: boolean
}
type HeightVariant = 'normal' | 'small' | 'extra-small'
const asideContext = React.createContext<{
    height: HeightVariant
    mini: boolean
    toggleMini: () => void
}>({ height: 'normal', mini: true, toggleMini: () => {} })

export const PageAside: React.FC<PageAsideProps> = ({ isMobileMenuOpen, onCloseMobileMenu, isMobile }) => {
    const isSmallHeight = useMediaQuery<Theme>('@media (min-height: 601px) and (max-height: 700px)', { noSsr: true })
    const isExtraSmallHeight = useMediaQuery<Theme>('@media (max-height: 600px)', { noSsr: true })

    const height: HeightVariant = React.useMemo(() => {
        if (!isMobile) {
            if (isSmallHeight) {
                return 'small'
            }
            if (isExtraSmallHeight) {
                return 'extra-small'
            }
        }
        return 'normal'
    }, [isMobile, isExtraSmallHeight, isSmallHeight])

    const theme = useTheme()
    const mini = useSelector(state => state.page.isAsideCollapsed) && !isMobile
    const asideWidth = useSelector(state => state.page.asideWidth)
    const dispatch = useDispatch()
    const toggleMini = useCallback(() => {
        dispatch(toggleAside())
    }, [dispatch])

    return (
        <StyledDrawer
            $asideWidth={asideWidth}
            variant={isMobile ? 'temporary' : 'permanent'}
            open={isMobileMenuOpen}
            onClose={onCloseMobileMenu}
        >
            <asideContext.Provider value={{ height, mini, toggleMini }}>
                <PageAsideHeader isMobile={isMobile} />
                <PageAsideMenu />
            </asideContext.Provider>
            <ErrorBoundary silent>{theme.extras?.pageAside?.extraComponent}</ErrorBoundary>
        </StyledDrawer>
    )
}

const avatarHeightMap: Record<HeightVariant, AvatarSize> = {
    normal: 'medium',
    small: 'medium',
    'extra-small': 'small-plus',
}

const spacingHeightMap: Record<HeightVariant, number> = {
    normal: 5,
    small: 3,
    'extra-small': 2,
}

const marginHeightMap: Record<HeightVariant, number> = {
    normal: 4,
    small: 2,
    'extra-small': 1,
}

const PageAsideHeader: React.FC<{ isMobile: boolean }> = ({ isMobile }) => {
    const { account } = useUserState()
    const theme = useTheme()
    const { height, mini } = useContext(asideContext)

    return (
        <Stack spacing={spacingHeightMap[height]} mb={marginHeightMap[height]}>
            <PageAsideLogoWrap>
                {!isMobile && !mini && (
                    <>
                        <Link to="/app">
                            <Logo
                                src={theme.extras?.logoType === 'white' ? LogiscoolLogoWhite : LogiscoolLogo}
                                alt="Logiscool"
                            />
                        </Link>
                        <ErrorBoundary silent>
                            <NotificationButtonWrap>
                                <NotificationButton />
                            </NotificationButtonWrap>
                        </ErrorBoundary>
                    </>
                )}
            </PageAsideLogoWrap>

            <Flex direction="column" align="center" pl={4} pr={4}>
                <CookieHunt name="aside-avatar">
                    <Link to={`/app/user/${account.username}`}>
                        <CurrentUserAvatar size={mini ? 'small' : avatarHeightMap[height] ?? 'big'} />
                    </Link>
                </CookieHunt>
                {!mini && (
                    <Box textAlign="center" mt={2}>
                        <StyledLink to={`/app/user/${account.username}`}>
                            <Username variant={mini || height === 'extra-small' ? 'h4' : 'h3'} component="span">
                                <Inline spacing={2}>
                                    <span>{getUserPrivateName(account)}</span>
                                    <UserBadge user={account} />
                                </Inline>
                            </Username>
                        </StyledLink>
                    </Box>
                )}
                {account.userNbr && (
                    <Box textAlign="center">
                        <UserNumber component="span">@{account.userNbr}</UserNumber>
                    </Box>
                )}
            </Flex>
        </Stack>
    )
}

const LogoutButton: React.FC<{ action: () => Promise<void> | void }> = ({ action }) => {
    const t = useTranslate()
    return (
        <SecondaryButton icon={<LogOutIcon />} onClick={action}>
            {t('general::logout')}
        </SecondaryButton>
    )
}

const PageAsideMenu = () => {
    const t = useTranslate()
    const { appVariantContent } = useAppVariantContent()
    const hasGrant = useHasGrant()
    const hasNewsAreaGrant = hasGrant('area.news')
    const hasCurriculumAreaGrant = hasGrant('area.classroom.curriculum')
    const hasMyGroupsAreaGrant = hasGrant('area.classroom.my-groups')
    const hasTimetableAreaGrant = hasGrant('area.classroom.timetable')
    const hasMyProjectsAreaGrant = hasGrant('area.my-projects')
    const hasCommunityAreaGrant = hasGrant('area.community')
    const hasChallengesAreaGrant = hasGrant('area.challenges')
    const hasEventsAreaGrant = hasGrant('area.events')

    return (
        <>
            <StyledList style={{ flex: 1 }}>
                {hasNewsAreaGrant &&
                    appVariantContent({
                        normal: <PageAsideNewsMenuItem />,
                        guest: <PageAsideWelcomeMenuItem />,
                        'open-day': <PageAsideWelcomeMenuItem />,
                    })}
                {appVariantContent({
                    normal: (
                        <>
                            {hasMyProjectsAreaGrant && <PageAsideMyProjectsMenuItem />}
                            {(hasTimetableAreaGrant || hasCurriculumAreaGrant) && <PageAsideClassroomMenuItem />}
                            {!hasTimetableAreaGrant && !hasCurriculumAreaGrant && hasMyGroupsAreaGrant && (
                                <PageAsideMyGroupsMenuItem />
                            )}
                            <PageAsideProfileMenuItem />
                            {hasCommunityAreaGrant && <PageAsideCommunityMenuItem />}
                            {hasChallengesAreaGrant && <PageAsideChallengesMenuItem />}
                            {hasEventsAreaGrant && <PageAsideEventMenuItem />}
                        </>
                    ),
                    'open-day': (
                        <>
                            {hasMyProjectsAreaGrant && <PageAsideOpenDayMenuItem />}
                            <PageAsideProfileMenuItem />
                            {hasCommunityAreaGrant && <PageAsideCommunityMenuItem />}
                            {hasChallengesAreaGrant && <PageAsideChallengesMenuItem />}
                            {hasEventsAreaGrant && <PageAsideEventMenuItem />}
                        </>
                    ),
                    guest: (
                        <>
                            {hasChallengesAreaGrant && <PageAsideChallengesMenuItem />}
                            {hasCommunityAreaGrant && <PageAsideCommunityMenuItem />}
                            {runtimeEnv.REACT_APP_ONBOARDING_ENABLED && <PageAsideEventMissionMenuItem />}
                        </>
                    ),
                })}
            </StyledList>
            <StyledDivider />
            <StyledList>
                <SecondaryButton icon={<SettingsIcon />} to="/app/settings">
                    {t('settings::label')}
                </SecondaryButton>
                <InClassModeConfirmProvider action={auth.logout} ActionWrapper={LogoutButton} />
            </StyledList>
        </>
    )
}

