interface PageAsideMenuItemProps {
    icon: React.ReactElement
    to?: string
    onClick?: () => void
    className?: string
}

const PageAsideMenuItem: React.FC<PageAsideMenuItemProps> = ({ children, to, onClick, icon, className }) => {
    const { height, mini } = useContext(asideContext)

    const body = (
        <>
            <StyledListItemIcon $height={height}>{icon}</StyledListItemIcon>
            {!mini && children}
        </>
    )

    let item
    if (to) {
        item = (
            <StyledListItem
                mini={mini}
                $height={height}
                className={className}
                component={NavLink}
                activeClassName="Mui-selected"
                to={to}
                onClick={onClick}
            >
                {body}
            </StyledListItem>
        )
    } else {
        item = (
            <StyledListItem mini={mini} $height={height} className={className} onClick={onClick}>
                {body}
            </StyledListItem>
        )
    }

    if (mini) {
        return (
            <Tooltip title={<>{children}</>} placement="right">
                <div>{item}</div>
            </Tooltip>
        )
    }

    return item
}

