import React, { useEffect } from 'react'
import { useTranslate } from 'lib/i18n/useTranslate'
import { Dialog, DialogContent, DialogTitle } from 'components/dialog'
import { Attendance } from '../../types/groupTypes'
import { api } from 'lib/api'
import { Loader } from 'components/Loader'
import { QRCodeSVG } from 'qrcode.react'
import { Avatar } from '../../../avatar'
import { Text } from 'components/Text'
import { Stack } from 'components/layout'
import styled from 'styled-components/macro'
import { Box } from '@material-ui/core'

export interface QRLoginModalProps {
    participant?: Attendance
    open: boolean
    onClose(): void
}

const HorizontalStack = styled(Box)`
    display: flex;
    align-items: center;
    gap: ${props => props.theme.spacing(0)};
    justify-content: flex-start;
`

export const QRLoginModal: React.FC<QRLoginModalProps> = ({ participant, open, onClose }) => {
    const t = useTranslate()
    const [status, setStatus] = React.useState<'idle' | 'loading' | 'success' | 'error'>('idle')
    const [uri, setURI] = React.useState<string | null>(null)

    useEffect(() => {
        if (open && participant?.username) {
            setStatus('loading')
            api.accounts.generateOneTimePassword(participant.username).then(password => {
                setURI(password)
                setStatus('success')
            })
        }
    }, [open, participant?.username])

    function close() {
        setStatus('idle')
        onClose()
    }

    return (
        <Dialog open={open} onClose={close}>
            <DialogTitle>{t('timesheet.attendance.qrLogin')}</DialogTitle>
            <DialogContent>
                <Stack spacing={4}>
                    <HorizontalStack>
                        <Avatar src={participant?.profileImage} size="tiny" />
                        <Text style={{ marginLeft: '.5rem' }}>{participant?.fullName || ''}</Text>
                        &nbsp;(
                        <Text>{participant?.alias || participant?.username || ''}</Text>)
                    </HorizontalStack>
                    {status === 'loading' && <Loader />}
                    {status === 'success' && uri && <QRCodeSVG value={uri} marginSize={4} size={256} />}
                </Stack>
            </DialogContent>
        </Dialog>
    )
}
