import React, { useState, useMemo } from 'react'
import styled from 'styled-components/macro'
import { CardContent } from '@material-ui/core'
import { Box } from 'components/layout'
import { Text } from 'components/Text'
import { Tutorial } from '../../../types/groupTypes'
import { ActionCard } from 'components/action-card'
import { AttachmentKindIcon } from './AttachmentKindIcon'
import { Dialog, DialogActions, DialogContent, DialogPrimaryButton, DialogTitle } from 'components/dialog'
import { useTranslate } from 'lib/i18n/useTranslate'
import { Card } from 'components/card'

const HorizontalLeftAlignedStack = styled(Box)`
    display: flex;
    align-items: flex-start;
    gap: ${props => props.theme.spacing(1)};
    justify-content: flex-start;
`

export const DocumentAttachment: React.FC<{ entry: Tutorial }> = ({ entry }) => {
    const t = useTranslate()
    const [open, setOpen] = useState(false)

    const extension = useMemo(() => entry.url.split('.').pop()?.toLowerCase(), [entry.url])

    const actionTypeOpenInTab = useMemo(() => {
        const extensionsInNewTab = [
            'pdf',
        ]
        for (const ext of extensionsInNewTab) {
            if (extension && extension.includes(ext || '')) {
                return true
            }
        }
        return false
    },[extension])

    const actionTypeDownload = useMemo(() => {
        const extensionsInNewTab = [
            'apk', 
            'doc', 
            'ev3', 
            'kodu', 
            'lmsp',
            'llsp3',
            'ppt', 
            'py',
            'rbxl',
            'rbxm', 
            'sb2',
            'xls',
        ]
        for (const ext of extensionsInNewTab) {
            if (extension && extension.includes(ext || '')) {
                return true
            }
        }
        return false
    },[extension])

    return (
        <>
            <ActionCard
                type="button"
                onClick={e => {
                    e.stopPropagation()
                    e.preventDefault()
                    if (actionTypeOpenInTab) {
                        window.open(entry.url, '_blank')
                    } 
                    else if (actionTypeDownload) {
                        const link = document.createElement('a');
                        link.href = entry.url;
                        link.download = `${entry.title}.${extension}`; // Optionally set a filename
                        document.body.appendChild(link);
                        link.click();
                        document.body.removeChild(link);
                    }
                    else {
                        setOpen(true)
                    }
                    return false
                }}
            >
                <CardContent style={{ padding: '0.5rem 0.7rem' }}>
                    <HorizontalLeftAlignedStack>
                        <AttachmentKindIcon kind={entry.kind} print={entry.print} />
                        <Text>{entry.title}</Text>
                    </HorizontalLeftAlignedStack>
                </CardContent>
            </ActionCard>
            <Dialog fullHeight fullWidth maxWidth="md" open={open} onClose={() => setOpen(false)}>
                <DialogTitle>{entry.title}</DialogTitle>
                <DialogContent>
                    <Box display="flex" flexDirection="column" style={{ height: '100%', gap: '1rem' }}>
                        {entry.print && (
                            <Card>
                                <Box margin={2}>
                                    <HorizontalLeftAlignedStack>
                                        <AttachmentKindIcon kind={entry.kind} print />
                                        <Text>
                                            {entry.printPerStudent &&
                                                t('timesheet.printPerStudent', {
                                                    defaultValue: 'You should print one for each student in the class.',
                                                })}
                                            {!entry.printPerStudent &&
                                                t('timesheet.printOne', {
                                                    defaultValue: 'You should print this for the group.',
                                                })}
                                        </Text>
                                    </HorizontalLeftAlignedStack>
                                </Box>
                            </Card>
                        )}
                        <iframe
                            style={{ flex: 1, border: 'none' }}
                            src={entry.url}
                            title={entry.title}
                            width="100%"
                            height="100%"
                            seamless
                        />
                    </Box>
                </DialogContent>
                <DialogActions>
                    <DialogPrimaryButton component={'a'} href={entry.url} download>
                        {t('general::download')}
                    </DialogPrimaryButton>
                </DialogActions>
            </Dialog>
        </>
    )
}
