import React from 'react'
import styled from 'styled-components/macro'
import { CardContent } from '@material-ui/core'
import { Box } from 'components/layout'
import { Text } from 'components/Text'
import { Tutorial } from '../../../types/groupTypes'
import { ActionCard } from 'components/action-card'
import { AttachmentKindIcon } from './AttachmentKindIcon'

const HorizontalLeftAlignedStack = styled(Box)`
    display: flex;
    align-items: flex-start;
    gap: ${props => props.theme.spacing(1)};
    justify-content: flex-start;
`

export const DownloadAttachment: React.FC<{ entry: Tutorial }> = ({ entry }) => {
    return (
        <ActionCard
            type="external-link"
            to={entry.url}
            onClick={e => {
                e.stopPropagation()
                return false
            }}
        >
            <CardContent style={{ padding: '0.5rem 0.7rem' }}>
                <HorizontalLeftAlignedStack>
                    <AttachmentKindIcon kind={entry.kind} />
                    <Text>{entry.title}</Text>
                </HorizontalLeftAlignedStack>
            </CardContent>
        </ActionCard>
    )
}
