import styled from 'styled-components/macro'
import { CardContent, CardHeader } from 'components/card'
import { Inline } from 'components/layout'
import React, { useMemo } from 'react'
import { ActionCard } from 'components/action-card'
import { RightAngleIcon } from 'components/icons'
import { useTranslate } from 'lib/i18n/useTranslate'
import { Theme, useMediaQuery } from '@material-ui/core'
import { sortCourseTypes } from './CurriculumPanel'

const HorizontalContent = styled(CardContent)`
    display: flex;
    align-items: center;
    gap: ${props => props.theme.spacing(4)};
    justify-content: space-between;
`

const NoPaddingHeader = styled(CardHeader)`
    padding: 0;
`

const ActionWrap = styled(Inline)`
    display: flex;
    flex-shrink: 0;
    align-items: center;
    font-weight: 600;
    justify-content: flex-end;
    text-align: right;
    color: ${props => props.theme.palette.primary.main};
`

export const SubjectCard: React.FC<{ entry: any, language: string }> = ({ entry, language }) => {
    const sortedCourseTypes = useMemo(() => {
        return sortCourseTypes(entry.courseTypes, language)
    }, [entry.courseTypes, language])
    const summary = sortedCourseTypes.length > 0 ? sortedCourseTypes[0]?.summary : entry.courseTypes[0]?.summary
    const t = useTranslate()
    const isMobile = useMediaQuery<Theme>(theme => theme.breakpoints.down('xs'), { noSsr: true })

    return (
        <ActionCard type="link" to={`/app/classroom/curriculum/${entry.id}`}>
            <HorizontalContent>
                <NoPaddingHeader
                    titleTypographyProps={{ variant: 'h4' }}
                    title={entry.name}
                    subheader={`${t(
                        t(`classroom::curriculum::${entry.kind}`, { defaultValue: entry.kind }),
                    )}  ${summary}`}
                />
                {!isMobile && (
                    <ActionWrap component="span" spacing={0}>
                        {t('general::details')}
                        <RightAngleIcon />
                    </ActionWrap>
                )}
            </HorizontalContent>
        </ActionCard>
    )
}
