import { useQuery } from 'react-query'
import { api } from '../../lib/api'
import dayjs, { Dayjs } from 'dayjs'
import { Attendances, LessonAndTimesheet, Timesheet } from './types/groupTypes'

export const useTermsDateQuery = () => {
    return useQuery(
        ['terms-date'],
        () => {
            return api.groups.getTermsDate()
        },
        { enabled: true, staleTime: Infinity },
    )
}

export const useCalendarQuery = (startDate: dayjs.Dayjs, endDate: dayjs.Dayjs) => {
    const start = startDate.format('YYYY-MM-DD')
    const end = endDate.format('YYYY-MM-DD')
    return useQuery(
        ['crm-calendar', start, end],
        () => {
            return api.groups.getCalendar(startDate, endDate)
        },
        { enabled: true, staleTime: Infinity },
    )
}

export const useGroupsQuery = () => {
    return useQuery(
        ['crm-groups'],
        () => {
            return api.groups.getGroups()
        },
        { enabled: true, staleTime: Infinity },
    )
}

export const useCurriculumQuery = (publishedOnly: boolean) => {
    return useQuery(
        ['curriculum'],
        () => {
            return api.groups.getCurriculum(publishedOnly)
        },
        { enabled: true, staleTime: Infinity },
    )
}

export const useCourseTypeQuery = (id: string, publishedOnly: boolean) => {
    return useQuery(
        ['course-type', id],
        () => {
            if (!id) {
                return Promise.resolve({
                    id: '',
                    name: '',
                    summary: '',
                    description: '',
                    subject: '',
                    lessons: [],
                    tutorials: [],
                    language: '',
                    version: 0,
                    chatRoom: '',
                })
            }
            return api.groups.getCourseType(id, publishedOnly)
        },
        { enabled: true, staleTime: Infinity },
    )
}

export const useLessonQuery = (courseTypeId: string, id: string, useCache: boolean) => {
    return useQuery(
        ['lesson', courseTypeId, id],
        () => {
            if (!courseTypeId || !id) {
                return Promise.resolve({} as LessonAndTimesheet)
            }
            return api.groups.getLesson(courseTypeId, id, useCache)
        },
        { enabled: true, staleTime: useCache ? Infinity : 0 },
    )
}

export const useTimesheetQuery = (id: string) => {
    return useQuery(
        ['timesheet', id],
        () => {
            if (!id) {
                return Promise.resolve({} as Timesheet)
            }
            return api.groups.getTimesheet(id)
        },
        { enabled: true, staleTime: 0, cacheTime: 0 },
    )
}

export const useAttendanceQuery = (groupId?: string, sessionId?: string, startTime?: Dayjs) => {
    return useQuery(
        ['attendance', groupId, sessionId],
        () => {
            if (!sessionId) {
                return Promise.resolve({} as Attendances)
            }
            if (groupId === 'undefined') groupId = undefined
            const timestamp = api.groups.getTimestampString(startTime)
            return api.groups.getAttendance(sessionId, timestamp, groupId)
        },
        { enabled: true, staleTime: 120000, refetchInterval: 120000 },
    )
}

export const useVimeoThumbnailQuery = (url: string) => {
    return useQuery(
        ['vimeo-oembed', url],
        async () => {
            let video
            try {
                const response = await fetch(`https://vimeo.com/api/oembed.json?url=${url}`)
                video = await response.json()
            } catch (e) {
                video = {}
            }
            return video?.thumbnail_url_with_play_button || ''
        },
        { enabled: true, staleTime: Infinity },
    )
}
