import React, { useEffect, useRef } from 'react'
import { TimesheetCodeGroupEntry } from '../../../types/groupTypes'
import { Box, Stack } from 'components/layout'
import styled from 'styled-components/macro'
import hljs from 'highlight.js'
import 'highlight.js/styles/github.css'
import { HTMLText } from './HTMLText'

const CodeBlock = styled.pre`
    margin: 0;
    padding: 1rem;
    border-radius: 4px;
    background-color: #f5f5f5;
    overflow-x: auto;
`

export const CodeGroupEntry: React.FC<{ entry: TimesheetCodeGroupEntry }> = ({ entry }) => {
    const codeRef = useRef<HTMLElement>(null)

    useEffect(() => {
        if (codeRef.current) {
            hljs.highlightElement(codeRef.current)
        }
    }, [entry.content.code, entry.content.language])

    return (
        <Stack spacing={2}>
            <HTMLText text={entry.content.details} />
            <Box>
                <CodeBlock>
                    <code ref={codeRef} className={entry.content.language}>
                        {entry.content.code}
                    </code>
                </CodeBlock>
            </Box>
        </Stack>
    )
}
