import React from 'react'
import { TimesheetVideoGroupEntry } from '../../../types/groupTypes'
import { EmbeddedVideo } from 'components/EmbeddedVideo'
import { Box, Stack } from 'components/layout'
import { HTMLText } from './HTMLText'

export const VideoGroupEntry: React.FC<{ entry: TimesheetVideoGroupEntry }> = ({ entry }) => {
    return (
        <Stack spacing={2}>
            <HTMLText text={entry.content.details} />
            <Box style={{ width: '50%' }}>
                <EmbeddedVideo type={entry.content.source as 'youtube' | 'vimeo'} id={entry.content.id} />
            </Box>
        </Stack>
    )
}
