import React from 'react'
import { useTranslate } from 'lib/i18n/useTranslate'
import { Button } from 'components/Button'
import { Box, Stack } from 'components/layout'
import { TextField } from 'components/form'
import { api } from 'lib/api'
import { Dialog, DialogActions, DialogContent, DialogTitle } from 'components/dialog'
import { Text } from 'components/Text'
import { Attendance } from '../../types/groupTypes'
import styled from 'styled-components/macro'
import { Avatar } from '../../../avatar'

const HorizontalStack = styled(Box)`
    display: flex;
    align-items: center;
    gap: ${props => props.theme.spacing(0)};
    justify-content: flex-start;
`

export interface PasswordResetModalProps {
    participant?: Attendance
    open: boolean
    onClose(): void
}

export const PasswordResetModal: React.FC<PasswordResetModalProps> = ({ participant, open, onClose }) => {
    const t = useTranslate()
    const [status, setStatus] = React.useState<'idle' | 'loading' | 'success' | 'error'>('idle')
    const [password, setPassword] = React.useState<string>('abcd1234')
    const isFilled = password.length >= 6 && password.length <= 64

    async function submitPasswordReset() {
        setStatus('loading')
        try {
            await api.accounts.resetPassword(participant?.scUserId || '', password)
            close()
        } catch (e) {
            console.error(e)
            setStatus('error')
            return
        }
    }

    function close() {
        setPassword('')
        setStatus('idle')
        onClose()
    }

    return (
        <Dialog open={open} disableBackdropClick={true} hideCloseButton={true}>
            <DialogTitle>{t('timesheet.attendance.resetPassword')}</DialogTitle>
            <DialogContent>
                <Stack spacing={4}>
                    <HorizontalStack>
                        <Avatar src={participant?.profileImage} size="tiny" />
                        <Text style={{ marginLeft: '.5rem' }}>{participant?.fullName || ''}</Text>
                        &nbsp;(
                        <Text>{participant?.alias || participant?.username || ''}</Text>)
                    </HorizontalStack>
                    <TextField
                        readOnly={status === 'loading'}
                        label={t('timesheet.attendance.newPassword')}
                        name="new-password"
                        value={password}
                        required
                        inputProps={{ minLength: 6, maxLength: 64 }}
                        helperText={isFilled || !password ? undefined : t('timesheet.attendance.pwLength')}
                        onChange={e => setPassword(e.target.value)}
                    />
                    {status === 'error' && <Text color="error">{t('timesheet.attendance.somethingWentWrong')}</Text>}
                </Stack>
            </DialogContent>
            <DialogActions>
                <Button disabled={status === 'loading'} variant="outlined" onClick={close}>
                    {t('general::cancel')}
                </Button>
                <Button isLoading={status === 'loading'} disabled={!isFilled} onClick={submitPasswordReset}>
                    {t('general::save')}
                </Button>
            </DialogActions>
        </Dialog>
    )
}
