export const dateStyles = (school: string, finishedAt: string): React.CSSProperties => {
    const isLong = school.length + finishedAt.length > 38
    return {
        fontSize: isLong ? '32pt' : '40pt',
    }
}

export const nameStyles = (name: string): React.CSSProperties => {
    const isLong = name.length > 22
    return {
        fontSize: isLong ? '55pt' : '74pt',
    }
}

export const getFontFamily = (locale: string) => {
    try {
        if (new Intl.Locale(locale).language === 'bg') {
            return 'Roboto, sans-serif'
        }
    } catch (e) {
        console.error(e)
    }
    return 'Raleway, sans-serif'
}

type FrameType =
    | 'blox'
    | 'mix'
    | 'a0'
    | 'text'
    | 'robot'
    | 'robotPrime'
    | 'video'
    | 'design'
    | 'minecraft'
    | 'courseMinecraftModding'
    | 'campMinecraftModding'
    | 'MinecraftMaze'
    | 'MinecraftMovie'
    | 'kodu'
    | 'ai'
    | 'aiPython'
    | 'aiBasic'
    | 'courseGenAI'
    | 'campGenAI'
    | 'rpg'
    | 'bizniscool'
    | 'roblox'
    | 'RobloxMultiverse'
    | 'openday'
    | 'digitalmission'
    | 'digitalmissionWinners'
    | 'workshop3d'
    | 'course3DDesign'
    | 'camp3DDesign'
    | 'workshopApp'
    | 'courseAppCreation'
    | 'campAppCreation'
    | 'workshopIllustration'
    | 'courseDigitalIllustration'
    | 'campDigitalIllustration'
    | 'workshopMinecraft'
    | 'digitalDiscovery'
    | 'digitalMission2021'
    | 'digitalMission2022'
    | 'digitalMission2023'
    | 'digitalMission2024'
    | 'scoolerMission'
    | 'workshopImagicharm'
    | 'courseImagiCharm'
    | 'campImagiCharm'
    | 'campChallengeDay'
    | 'explorerJuniorSpike'
    | 'explorerJuniorWedo'
    | 'explorerJuniorBeebot'
    | 'explorerCreative'
    | 'explorerGamer'
    | 'training'

const getFrameSrc = (type: FrameType, locale: string) => {
    return `${api.baseUrl}/certificates/frame-image-for-kind?kind=${type}&locale=${locale}`
}
