import React from 'react'
import { Tutorial } from '../../../types/groupTypes'
import { AttachmentEntry } from './AttachmentEntry'

export const AttachmentList: React.FC<{ entries: Tutorial[] }> = ({ entries }) => {
    const sortedEntries = entries.filter(e => e.published).sort((a, b) => a.index - b.index)
    const videoEntries = sortedEntries.filter(entry => entry.kind === 'video/vimeo')

    return (
        <>
            {sortedEntries.map(tutorial => (
                <AttachmentEntry videoEntries={videoEntries} entry={tutorial} key={tutorial.id} />
            ))}
        </>
    )
}
