import { ActionType } from 'typesafe-actions'
import * as inClassActions from './inClassActions'
import { CalendarEntryEventData } from '../types/groupTypes'

// Action types
export const SET_CALENDAR_EVENT = 'INCLASS/SET_CALENDAR_EVENT'
export const SET_URL = 'INCLASS/SET_URL'
export const SET_FEEDBACK_MODAL = 'INCLASS/SET_FEEDBACK_MODAL'
export const TOGGLE_ENABLED = 'INCLASS/TOGGLE_ENABLED'
export const GROUP_COMPLETED = 'INCLASS/GROUP_COMPLETED'
export const GROUP_INCOMPLETE = 'INCLASS/GROUP_INCOMPLETE'
export const TOGGLE_ENTRY = 'INCLASS/TOGGLE_ENTRY'
export const REPLACE_STATE = 'INCLASS/REPLACE_STATE'

// State interface
export interface InClassReducerState {
    enabled: boolean
    feedbackModalOpen: boolean
    url?: string
    sessionId?: string
    groupId?: string
    calendarEvent?: CalendarEntryEventData | null
    startTime: number
    version: number
    totalDuration: number
    entryGroupCounters: number[]
    entryGroupDurations: number[]
    entryCheckboxes: { value: boolean; duration: number; active: boolean }[][]
    groupCheckboxes: boolean[]
}

export type InClassAction = ActionType<typeof inClassActions>
