import React from 'react'
import { TimesheetGroupEntry } from '../../../types/groupTypes'
import { TextGroupEntry } from './TextGroupEntry'
import { TaskGroupEntry } from './TaskGroupEntry'
import { QuizGroupEntry } from './QuizGroupEntry'
import { ChallengeGroupEntry } from './ChallengeGroupEntry'
import { VideoGroupEntry } from './VideoGroupEntry'
import { CodeGroupEntry } from './CodeGroupEntry'
import { useInClassContext } from '../InClassModeCard'
import styled from 'styled-components/macro'
import { Box } from 'components/layout'
import { Checkbox, useTheme } from '@material-ui/core'

const GroupEntryViewContent: React.FC<{
    entry: TimesheetGroupEntry
    nextEntry?: TimesheetGroupEntry
    sessionId?: string
}> = ({ entry, nextEntry, sessionId }) => {
    switch (entry.type) {
        case 'text':
            return <TextGroupEntry entry={entry} nextEntry={nextEntry} />
        case 'task':
            return <TaskGroupEntry entry={entry} />
        case 'quiz':
            return <QuizGroupEntry entry={entry} sessionId={sessionId} />
        case 'challenge':
            return <ChallengeGroupEntry entry={entry} />
        case 'video':
            return <VideoGroupEntry entry={entry} />
        case 'code':
            return <CodeGroupEntry entry={entry} />
        default:
            return <pre>{JSON.stringify(entry, null, 2)}</pre>
    }
}

const HorizontalLeftAlignedStack = styled(Box)`
    display: flex;
    align-items: center;
    gap: ${props => props.theme.spacing(1)};
    justify-content: flex-start;
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
    border-radius: 1rem;
`

const StyledCheckbox = styled(Checkbox)`
    margin-left: 1rem;
    margin-right: 0.5rem;
    color: ${props => props.theme.palette.primary.main};
`

export const GroupEntryView: React.FC<{
    entry: TimesheetGroupEntry
    nextEntry?: TimesheetGroupEntry
    groupIndex: number
    entryIndex: number
    sessionId?: string
}> = ({ entry, nextEntry, groupIndex, entryIndex, sessionId }) => {
    const { isInClassMode, isEntryCompleted, isGroupCompleted, toggleEntry, hasMultipleEntries } = useInClassContext(
        groupIndex,
        entryIndex,
    )
    const [hover, setHover] = React.useState(false)
    const theme = useTheme()
    const checkboxVisible = entry.isActive && hasMultipleEntries
    const opacity = isInClassMode && checkboxVisible && isEntryCompleted && !isGroupCompleted ? 0.7 : 1

    return (
        <HorizontalLeftAlignedStack
            style={
                hover
                    ? {
                          borderBottom: `1px solid ${theme.palette.primary.dark}`,
                          borderTop: `1px solid ${theme.palette.primary.dark}`,
                          opacity,
                      }
                    : {
                          opacity,
                      }
            }
        >
            {isInClassMode && (
                <StyledCheckbox
                    color="primary"
                    style={{ visibility: checkboxVisible ? 'visible' : 'hidden' }}
                    checked={isEntryCompleted}
                    onChange={toggleEntry}
                    onMouseEnter={() => setHover(true)}
                    onMouseLeave={() => setHover(false)}
                />
            )}
            <GroupEntryViewContent entry={entry} nextEntry={nextEntry} sessionId={sessionId} />
        </HorizontalLeftAlignedStack>
    )
}
