import React from 'react'
import { MenuItem, MenuItemTypeMap } from '@material-ui/core'
import { OverridableComponent } from '@material-ui/core/OverridableComponent'
import styled from 'styled-components/macro'
import { transparentize } from 'polished'
import { Tooltip } from 'components/Tooltip'
import { usePopupMenuContext } from './popupMenuContext'

type Props = MenuItemTypeMap['props'] & {
    disabledTooltip?: string
    onClick?: (e: React.MouseEvent<HTMLLIElement, MouseEvent>) => any
}

export const PopupMenuItem: OverridableComponent<
    MenuItemTypeMap & { props: MenuItemTypeMap['props'] & { disabledTooltip?: string, onClick?: any, handleClose?: boolean } }
> = (React.forwardRef as any)(({ button, disabledTooltip, onClick, ...rest }: Props, ref: any) => {
    const { close } = usePopupMenuContext();
    const realOnClick = (e: React.MouseEvent<HTMLLIElement, MouseEvent>) => {
        if (onClick) onClick(e);
        close()
    }
    if (disabledTooltip) {
        return (
            <Tooltip title={disabledTooltip}>
                <span>
                    <Root disabled ref={ref} {...rest} onClick={realOnClick} />
                </span>
            </Tooltip>
        )
    }
    return <Root ref={ref} {...rest} onClick={realOnClick} />
})

export const CompactPopupMenuItem: OverridableComponent<
    MenuItemTypeMap & { props: MenuItemTypeMap['props'] & { disabledTooltip?: string, onClick?: any } }
> = (React.forwardRef as any)(({ button, disabledTooltip, onClick, ...rest }: Props, ref: any) => {
    const { close } = usePopupMenuContext();
    const realOnClick = (e: React.MouseEvent<HTMLLIElement, MouseEvent>) => {
        if (onClick) onClick(e);
        close()
    }
    if (disabledTooltip) {
        return (
            <Tooltip title={disabledTooltip}>
                <span>
                    <CompactRoot disabled ref={ref} {...rest} onClick={realOnClick} />
                </span>
            </Tooltip>
        )
    }
    return <CompactRoot ref={ref} {...rest} onClick={realOnClick} />
})

const Root = styled(MenuItem)`
    padding: ${props => props.theme.spacing(3)};
    border-radius: ${props => props.theme.shape.borderRadius}px;

    .MuiListItemIcon-root {
        min-width: 1.8rem;
        color: ${props => transparentize(0.2, props.theme.palette.text.primary)};
    }
`

const CompactRoot = styled(MenuItem)`
    padding-top: ${props => props.theme.spacing(1)};
    padding-bottom: ${props => props.theme.spacing(1)};
    padding-left: ${props => props.theme.spacing(3)};
    padding-right: ${props => props.theme.spacing(3)};
    border-radius: ${props => props.theme.shape.borderRadius}px;

    .MuiListItemIcon-root {
        min-width: 1.8rem;
        color: ${props => transparentize(0.2, props.theme.palette.text.primary)};
    }
`
