import produce from 'immer'
import { AnyAction, Reducer } from 'redux'
import { useSelector } from '.'

export interface PageState {
    asideWidth: string
    isAsideCollapsed: boolean
    contentWidth: string
    contentColumnWidth: string
    contentColumnHightlightWidth: string
    contentPageWidth: string
}

export const ASIDE_WIDTH_NORMAL = '16rem'
export const ASIDE_WIDTH_MINI = '6rem'

/* * Content width options: the column is narrower than the page width.
    * - normal: 600px column width, 70rem page width
    * - wide: 1200px column width, 140rem page width
    */
export const CONTENT_WIDTH_NORMAL = 'normal'
export const CONTENT_WIDTH_WIDE = 'wide'
export const CONTENT_COLUMN_WIDTH_NORMAL = '600px'
export const CONTENT_COLUMN_WIDTH_WIDE = '1200px'
export const CONTENT_COLUMN_HIGHLIGHT_WIDTH_NORMAL = '800px'
export const CONTENT_COLUMN_HIGHLIGHT_WIDTH_WIDE = '1600px'
export const CONTENT_PAGE_WIDTH_NORMAL = '70rem'
export const CONTENT_PAGE_WIDTH_WIDE = '140rem'

export const TOGGLE_ASIDE = 'PAGE/TOGGLE_ASIDE'
export const SET_ASIDE_COLLAPSED = 'PAGE/SET_ASIDE_COLLAPSED'
export const SET_CONTENT_WIDTH = 'PAGE/SET_CONTENT_WIDTH'

export function toggleAside() {
    return {
        type: TOGGLE_ASIDE,
        payload: {},
    } as const
}
export function setAsideCollapsed(value: boolean) {
    return {
        type: SET_ASIDE_COLLAPSED,
        payload: value,
    } as const
}

export function setContentWidth(width: string) {
    return {
        type: SET_CONTENT_WIDTH,
        payload: width,
    } as const
}

const CONTENT_WIDTH_STORAGE_KEY = '@my-logiscool/contentWidth'

const contentColumnWidthValues: Record<string, string> = {
    normal: CONTENT_COLUMN_WIDTH_NORMAL,
    wide: CONTENT_COLUMN_WIDTH_WIDE
}

const contentColumnHighlighWidthValues: Record<string, string> = {
    normal: CONTENT_COLUMN_HIGHLIGHT_WIDTH_NORMAL,
    wide: CONTENT_COLUMN_HIGHLIGHT_WIDTH_WIDE
}

const contentPageWidthValues: Record<string, string> = {
    normal: CONTENT_PAGE_WIDTH_NORMAL,
    wide: CONTENT_PAGE_WIDTH_WIDE
}

export function storeContentWidth(width: string) {
    window.localStorage.setItem(CONTENT_WIDTH_STORAGE_KEY, width)
}

export function getStoredContentWidth(): string {
    return window.localStorage.getItem(CONTENT_WIDTH_STORAGE_KEY) || 'normal'
}

export function getContentColumnWidth(width?: string): string {
    if (!width) {
        width = getStoredContentWidth()
    }
    return contentColumnWidthValues[width] || CONTENT_COLUMN_WIDTH_NORMAL
}

export function getContentColumnHighlighWidth(width?: string): string {
    if (!width) {
        width = getStoredContentWidth()
    }
    return contentColumnHighlighWidthValues[width] || CONTENT_COLUMN_HIGHLIGHT_WIDTH_NORMAL
}

export function getContentPageWidth(width?: string): string {
    if (!width) {
        width = getStoredContentWidth()
    }
    return contentPageWidthValues[width] || CONTENT_PAGE_WIDTH_NORMAL
}

export const pageReducer: Reducer<PageState, AnyAction> = (
    state = {
        asideWidth: ASIDE_WIDTH_NORMAL, 
        isAsideCollapsed: false,
        contentWidth: getStoredContentWidth(),
        contentColumnWidth: getContentColumnWidth(),
        contentColumnHightlightWidth: getContentColumnHighlighWidth(),
        contentPageWidth: getContentPageWidth(),
    },
    action,
) => {
    switch (action.type) {
        case TOGGLE_ASIDE:
            return produce(state, draft => {
                const isCollapsed = !state.isAsideCollapsed
                draft.isAsideCollapsed = isCollapsed
                draft.asideWidth = isCollapsed ? ASIDE_WIDTH_MINI : ASIDE_WIDTH_NORMAL
            })

        case SET_ASIDE_COLLAPSED:
            return produce(state, draft => {
                const isCollapsed = action.payload
                draft.isAsideCollapsed = isCollapsed
                draft.asideWidth = isCollapsed ? ASIDE_WIDTH_MINI : ASIDE_WIDTH_NORMAL
            })

        case SET_CONTENT_WIDTH:
            return produce(state, draft => {
                draft.contentWidth = action.payload
                draft.contentColumnWidth = getContentColumnWidth(action.payload)
                draft.contentColumnHightlightWidth = getContentColumnHighlighWidth(action.payload)
                draft.contentPageWidth = getContentPageWidth(action.payload)
            })
        
        default:
            return state
    }
}

export const useColumnWidth = () => {
    const contentWidth = useSelector((state) => state.page.contentColumnWidth)
    return contentWidth
}

export const useColumnHighlightWidth = () => {
    const contentWidth = useSelector((state) => state.page.contentColumnHightlightWidth)
    return contentWidth
}

export const usePageWidth = () => {
    const contentWidth = useSelector((state) => state.page.contentPageWidth)
    return contentWidth
}