import { action } from 'typesafe-actions'
import {
    SET_CALENDAR_EVENT,
    SET_URL,
    SET_FEEDBACK_MODAL,
    TOGGLE_ENABLED,
    GROUP_COMPLETED,
    GROUP_INCOMPLETE,
    TOGGLE_ENTRY, InClassReducerState, REPLACE_STATE,
} from './inClassTypes'
import { CalendarEntryEventData, Timesheet } from '../types/groupTypes'

export const setCalendarEvent = (calendarEvent?: CalendarEntryEventData | null) =>
    action(SET_CALENDAR_EVENT, calendarEvent)

export const setUrl = (url: string) => 
    action(SET_URL, url)

export const setFeedbackModal = (isOpen: boolean) => 
    action(SET_FEEDBACK_MODAL, isOpen)

export const toggleEnabled = (payload: { timesheet?: Timesheet }) => 
    action(TOGGLE_ENABLED, payload)

export const groupCompleted = (index: number) => 
    action(GROUP_COMPLETED, index)

export const groupIncomplete = (index: number) => 
    action(GROUP_INCOMPLETE, index)

export const toggleEntry = (index: number, entryIndex: number, value: boolean) => 
    action(TOGGLE_ENTRY, { index, entryIndex, value, timestamp: Date.now() })

export const replaceState = (state: InClassReducerState) =>
    action(REPLACE_STATE, { value: state })