import type { StoreState } from '../store'
import { Dispatch } from 'react'
import { AnyAction } from 'redux'
import { replaceState } from '../../../features/classroom/store'

function loadState() {
    const storedInClassState = window.localStorage.getItem('@shared-classroom-state/inClass')
    if (storedInClassState) {
        try {
            return JSON.parse(storedInClassState)
        } catch (e) {
            console.error('Failed to load in-class state:', e)
        }
    }
}

const sourceId = Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15)

export const classroomStateShareMiddleware = (
    username: string,
    getState: () => StoreState,
    dispatch: Dispatch<AnyAction>,
) => {
    const originalState = getState()
    let lastVersion = 0

    const parsedInClassState = loadState()
    if (
        parsedInClassState &&
        parsedInClassState.enabled &&
        parsedInClassState?.calendarEvent?.startTime &&
        parsedInClassState.username === username
    ) {
        console.log('Loaded in-class state:', parsedInClassState)
        const dateSaved = new Date(parsedInClassState.calendarEvent.startTime).getDate()
        const dateNow = new Date().getDate()
        if (dateSaved === dateNow) {
            lastVersion = parsedInClassState.version
            dispatch(replaceState(parsedInClassState))
        }
    }

    window.addEventListener('message', event => {
        if (
            event.origin === window.origin &&
            event.data.type === '@mylogiscool/classroom-state-share' &&
            event.data.sourceId !== sourceId &&
            event.data.username === username
        ) {
            console.log('Received in-class state:', event.data.sourceId + '/' + event.data.newState.version)
            dispatch(replaceState(event.data.newState))
        }
    })

    return () => {
        const newState = getState()
        if (newState.inClass !== originalState.inClass) {
            if (lastVersion !== newState.inClass.version) {
                lastVersion = newState.inClass.version
                const extendedState = {
                    ...newState.inClass,
                    username,
                }
                window.localStorage.setItem('@shared-classroom-state/inClass', JSON.stringify(extendedState))
                window.postMessage(
                    { type: '@mylogiscool/classroom-state-share', newState: newState.inClass, sourceId, username },
                    window.origin,
                )
                console.log('Saved and sent in-class state:', newState.inClass.version)
            } else {
                console.log('Skipped in-class state:', newState.inClass.version)
            }
        }
    }
}
