import React from 'react'
import { Tutorial } from '../../../types/groupTypes'
import { VimeoAttachment } from './VimeoAttachment'
import { DocumentAttachment } from './DocumentAttachment'
import { DownloadAttachment } from './DownloadAttachment'
import { Card } from 'components/card'

export const AttachmentEntry: React.FC<{ entry: Tutorial; videoEntries: Tutorial[] }> = ({ entry, videoEntries }) => {
    switch (entry.kind) {
        case 'video/vimeo':
            return <VimeoAttachment entry={entry} videoEntries={videoEntries} />
        case 'document':
            return <DocumentAttachment entry={entry} />
        case 'document/zip':
            return <DownloadAttachment entry={entry} />
        default:
            return (
                <Card>
                    <pre>{JSON.stringify(entry, null, 2)}</pre>
                </Card>
            )
    }
}
