import React from 'react'
import { Skeleton as MuiSkeleton, SkeletonProps as MuiSkeletonProps } from '@material-ui/lab'
import styled from 'styled-components/macro'

interface SkeletonProps extends MuiSkeletonProps {}

export const LargeSkeleton: React.FC<SkeletonProps> = props => {
    return <StyledLargeSkeleton {...props} />
}

const StyledLargeSkeleton = styled(MuiSkeleton)`
    border-radius: ${props => props.theme.shape.borderRadius}px;
    transform: scale(1, 0.8);
    height: 8rem;
`

export const MediumSkeleton: React.FC<SkeletonProps> = props => {
    return <StyledMediumSkeleton {...props} />
}

const StyledMediumSkeleton = styled(MuiSkeleton)`
    border-radius: ${props => props.theme.shape.borderRadius}px;
    transform: scale(1, 0.8);
    height: 4rem;
`

export const SmallSkeleton: React.FC<SkeletonProps> = props => {
    return <StyledSmallSkeleton {...props} />
}

const StyledSmallSkeleton = styled(MuiSkeleton)`
    border-radius: ${props => props.theme.shape.borderRadius}px;
    height: 2rem;
`

export const Skeleton: React.FC<SkeletonProps> = props => {
    return <StyledSkeleton {...props} />
}

const StyledSkeleton = styled(MuiSkeleton)`
    border-radius: ${props => props.theme.shape.borderRadius}px;
`
