import React, { useMemo } from 'react'
import { Panel, PanelHeader, PanelTitle } from '../../../../components/panel'
import { Stack } from '../../../../components/layout'
import { SubjectCard } from './SubjectCard'
import { CourseType } from 'features/classroom/types/groupTypes'

export interface CurriculumFilters {
    subjectKind: string
    language: string
    search?: string
}

export const sortCourseTypes = (courseTypes: CourseType[], language: string) => {
    return [...courseTypes]
            .filter(courseType => courseType.language === language)
            .sort((a, b) => {
                const versionA = a.version || 0
                const versionB = b.version || 0
                return versionB - versionA
            });
}

export const CurriculumPanel: React.FC<{ entry: any; filters: CurriculumFilters }> = ({ entry, filters }) => {
    const filteredSubjects = useMemo(
        () =>
            entry.subjects.filter(
                (subject: any) =>
                    (filters.subjectKind === 'all' || subject.kind === filters.subjectKind) &&
                    (!filters.search || subject.name.toLowerCase().includes(filters.search.toLowerCase())),
            ),
        [entry, filters],
    )

    if (filteredSubjects.length === 0) {
        return null
    }

    filteredSubjects.sort((a: any, b: any) => {
        if (a.subjectIndex === undefined && b.subjectIndex === undefined) {
            return 0; // Both are undefined, keep their relative order
        }
        if (a.subjectIndex === undefined) {
            return 1; // Place subjects without subjectIndex at the end
        }
        if (b.subjectIndex === undefined) {
            return -1; // Place subjects without subjectIndex at the end
        }
        return a.subjectIndex - b.subjectIndex;
    })

    return (
        <Panel>
            <PanelHeader>
                <PanelTitle>{entry.name}</PanelTitle>
            </PanelHeader>
            <Stack spacing={2} margin={4}>
                {filteredSubjects.map((subject: any) => (
                    <SubjectCard key={subject.id} entry={subject} language={filters.language} />
                ))}
            </Stack>
        </Panel>
    )
}
