import React, { useContext } from 'react'
import { useTranslate } from 'lib/i18n/useTranslate'
import { Button } from 'components/Button'
import { Stack } from 'components/layout'
import { FormControl, FormLabel, TextField } from 'components/form'
import { api } from 'lib/api'
import { useUserState } from 'contexts/UserContext'
import MoodRating from './elements/MoodRating'
import { Dialog, DialogActions, DialogContent, DialogTitle } from 'components/dialog'
import { Text } from 'components/Text'
import { InClassContext } from './InClassModeCard'
import { setFeedbackModal } from '../../store'

export interface FeedbackModalProps {
    timesheet: string
    lessonType: string
    lessonIndex: number
    lessonLength: number
    courseType: string
    progress: number
    numberOfStudents: number
}

export const FeedbackModal: React.FC<FeedbackModalProps> = props => {
    const t = useTranslate()
    const user = useUserState()
    const [status, setStatus] = React.useState<'idle' | 'loading' | 'success' | 'error'>('idle')
    const [studentRatingValue, setStudentRatingValue] = React.useState<number | null>(null)
    const [trainerRatingValue, setTrainerRatingValue] = React.useState<number | null>(null)
    const [commentValue, setCommentValue] = React.useState<string | null>(null)
    const isFilled = studentRatingValue !== null && trainerRatingValue !== null
    const {
        state: { feedbackModalOpen },
        dispatch,
    } = useContext(InClassContext)

    async function submitFeedback() {
        setStatus('loading')
        try {
            await api.groups.submitTimesheetFeedback({
                school: user.crm.lastSchoolId,
                timesheet: props.timesheet,
                studentsRating: studentRatingValue,
                teachersRating: trainerRatingValue,
                comment: commentValue,
                lessonLength: props.lessonLength,
                lessonType: props.lessonType,
                lessonIndex: props.lessonIndex,
                courseType: props.courseType,
                progress: props.progress,
                endClass: true,
                numberOfStudents: props.numberOfStudents,
                status: !commentValue ? 'resolved' : 'waiting',
            })
            close()
        } catch (e) {
            console.error(e)
            setStatus('error')
            return
        }
    }

    function close() {
        setStudentRatingValue(null)
        setTrainerRatingValue(null)
        setCommentValue(null)
        setStatus('idle')
        dispatch(setFeedbackModal(false))
    }

    return (
        <Dialog open={feedbackModalOpen} disableBackdropClick={true} hideCloseButton={true}>
            <DialogTitle>{t('timesheet.feedbackModal.trainersFeedback')}</DialogTitle>
            <DialogContent>
                <Stack spacing={4}>
                    <Text>{t('timesheet.feedbackModal.subtitle')}</Text>
                    <FormControl>
                        <FormLabel>{t('timesheet.feedbackModal.trainerSatisfaction')}</FormLabel>
                        <MoodRating
                            value={trainerRatingValue}
                            name="teachers-rating-modal"
                            onChange={(_, v) => setTrainerRatingValue(v)}
                        />
                    </FormControl>
                    <FormControl>
                        <FormLabel>{t('timesheet.feedbackModal.studentSatisfaction')}</FormLabel>
                        <MoodRating
                            value={studentRatingValue}
                            name="students-rating"
                            onChange={(_, v) => setStudentRatingValue(v)}
                        />
                    </FormControl>
                    <TextField
                        readOnly={status === 'loading'}
                        label={t(`timesheet.feedbackModal.comments`)}
                        multiline
                        name="feedback-comments"
                        rows={4}
                        value={commentValue || ''}
                        inputProps={{ maxLength: 500 }}
                        onChange={e => {
                            setCommentValue(e.target.value)
                        }}
                    ></TextField>
                </Stack>
            </DialogContent>
            <DialogActions>
                <Button disabled={status === 'loading'} variant="outlined" onClick={close}>
                    {t('timesheet.feedbackModal.skipButton')}
                </Button>
                <Button isLoading={status === 'loading'} disabled={!isFilled} onClick={submitFeedback}>
                    {t('timesheet.feedbackModal.submit')}
                </Button>
            </DialogActions>
        </Dialog>
    )
}
