import React, { useEffect } from 'react'
import { Box, Stack } from 'components/layout'

import { TimesheetQuizGroupEntry } from '../../../types/groupTypes'
import { HTMLText } from './HTMLText'
import { Button, ButtonProps } from 'components/Button'
import { runtimeEnv } from 'utils/env'
import styled from 'styled-components/macro'
import { useTranslate } from 'lib/i18n/useTranslate'
import { useHasGrant } from '../../../../../lib/grants'

const HorizontalLeftAlignedStack = styled(Box)`
    display: flex;
    align-items: center;
    gap: ${props => props.theme.spacing(2)};
    justify-content: flex-start;
`

export const CopyButton: React.FC<ButtonProps & { value: string }> = ({
    disabled,
    onClick,
    children,
    value,
    ...props
}) => {
    const [state, setState] = React.useState('normal')
    const t = useTranslate()

    useEffect(() => {
        if (state === 'done' || state === 'error') {
            const timeout = setTimeout(() => setState('normal'), 1000)
            return () => clearTimeout(timeout)
        }
    }, [state])

    const handleClick = async () => {
        setState('progress')
        try {
            await navigator.clipboard.writeText(value)
            setState('done')
        } catch (error) {
            setState('error')
        }

        if (onClick) onClick()
    }

    return (
        <Button {...props} onClick={handleClick} disabled={disabled || state !== 'normal'}>
            {state === 'normal' && children}
            {state === 'progress' && t('general.copyProgress', { defaultValue: 'Copying...' })}
            {state === 'done' && t('general.copySuccess', { defaultValue: 'Copied' })}
            {state === 'error' && t('general.copyError', { defaultValue: 'Failed to copy' })}
        </Button>
    )
}

export const QuizGroupEntry: React.FC<{ entry: TimesheetQuizGroupEntry; sessionId?: string }> = ({
    entry,
    sessionId,
}) => {
    const t = useTranslate()
    const hasGrant = useHasGrant()

    const canHost = hasGrant({ product: 'edup.quiz', feature: 'host' })
    const canPreview = hasGrant({ product: 'edup.quiz', feature: 'preview' })
    const canSelfHost = hasGrant({ product: 'edup.quiz', feature: 'self-host' })
    const isTrialOrSalesDemo =
        hasGrant({ product: 'edup', feature: 'sales-demo' }) || hasGrant({ product: 'edup', feature: 'trial-demo' })

    // As of May 2025, we want the generic description only.
    const description = /*entry.content.details ||*/ t('timesheet.quiz.description')

    const getURL = (mode: string) => {
        return `${runtimeEnv.REACT_APP_QUIZ_URL}/${mode}/${entry.content.quiz_template_id}${
            mode === 'start' && sessionId ? `/session/${sessionId}` : ''
        }`
    }
    const handleClick = (mode: string) => {
        window.open(getURL(mode), '_blank')
    }

    //TODO:
    //<RichText content={translate(isEvent ? 'timesheet.quiz.eventDescription' : 'timesheet.quiz.description')} />

    return (
        <Stack>
            {description && <HTMLText text={description} />}
            <HorizontalLeftAlignedStack>
                {canHost && !isTrialOrSalesDemo && (
                    <Button variant="contained" onClick={() => handleClick('start')}>
                        {t('timesheet.startQuiz', { defaultValue: 'Start Quiz' })}
                    </Button>
                )}
                {canPreview && (
                    <Button variant="outlined" onClick={() => handleClick('preview')}>
                        {t('timesheet.previewQuiz', { defaultValue: 'Preview Quiz' })}
                    </Button>
                )}
                {isTrialOrSalesDemo && (
                    <Button variant="contained" onClick={() => handleClick('room-for')}>
                        {t('timesheet.startQuiz', { defaultValue: 'Start Quiz' })}
                    </Button>
                )}
                {canSelfHost && !isTrialOrSalesDemo && (
                    <CopyButton variant="outlined" value={getURL('room-for')}>
                        {t('timesheet.copyShLink', { defaultValue: 'Copy Self-Host Link' })}
                    </CopyButton>
                )}
            </HorizontalLeftAlignedStack>
        </Stack>
    )
}
