import * as React from 'react'
import { CheckCircleIcon, TimesCircleIcon } from 'components/icons'
import styled from 'styled-components/macro'
import { IconContainerProps, Rating, RatingProps } from '@material-ui/lab'

const StyledRating = styled(Rating)(({ theme }) => ({
    '& .MuiRating-icon': {
        marginRight: theme.spacing(0.8),
    },
    '& .MuiRating-iconFilled': {
        color: theme.palette.primary.main,
    },
    marginRight: theme.spacing(1),
}))

const customIcons: {
    [index: string]: {
        icon: React.ReactElement<unknown>
        label: string
    }
} = {
    1: {
        icon: <TimesCircleIcon />,
        label: 'Not Present',
    },
    2: {
        icon: <CheckCircleIcon />,
        label: 'Present',
    },
}

function IconContainer(props: IconContainerProps, selectedValue: number | null | undefined) {
    const { value, ...other } = props
    return (
        <span {...other} className={`MuiRating-icon MuiRating-icon${value === selectedValue ? 'Filled' : 'Empty'}`}>
            {customIcons[value].icon}
        </span>
    )
}

export default function AttendanceSwitch(props: RatingProps) {
    const createIconContainer = (_value: number | null | undefined) => {
        return (props: IconContainerProps) => IconContainer(props, _value)
    }

    return (
        <StyledRating
            {...props}
            defaultValue={0}
            max={2}
            IconContainerComponent={createIconContainer(props.value)}
            getLabelText={(value: number) => customIcons[value].label}
        />
    )
}
