import dayjs, { Dayjs } from 'dayjs'
import { ApiBase } from 'lib/api/ApiBase'
import {
    Attendances,
    CalendarEntry,
    CalendarEntryRaw,
    CourseType,
    Group,
    GroupRaw,
    LessonAndTimesheet,
    Timesheet,
} from '../types/groupTypes'
import { removeTokenFromParams } from 'lib/api'

function prepareTime(time: Date) {
    const raw = new Date(time)
    return dayjs().set('hours', raw.getUTCHours()).set('minutes', raw.getUTCMinutes())
}

function prepareDateTime(time: Date | string) {
    if (typeof time === 'string') {
        return dayjs(time.replace(/Z$/, ""))
    }
    const raw = new Date(time)
    return dayjs(time).set('hours', raw.getHours()).set('minutes', raw.getMinutes())
}

export class GroupsApi extends ApiBase {
    uploadImage = async (file: File) => {
        const formData = new FormData()
        formData.append('name', file.name)
        formData.append('kind', file.type)
        formData.append('metadata', JSON.stringify({ public: true }))
        formData.append('folder', '57ce8756f714a714cd6f9b2f') //timesheets-data folder
        formData.append('file', file)
        const resp = await this.client.post('files/upload', formData)
        const id = resp.data.id
        return `${this.client.defaults.baseURL}/files/${id}/data`
    }

    submitTimesheetFeedback = async (feedback: any) => {
        const res = await this.client.post('timesheet-surveys', feedback)
        return res.data
    }

    getTermsDate = async () => {
        const res = await this.client.get<{ value: string }>(
            'apps/5b9633e5c78855e09bbb7a55/settings/trainer-portal-terms?fields=value',
        )
        return res.data
    }

    getCalendar = async (startDate: dayjs.Dayjs, endDate: dayjs.Dayjs) => {
        const start = startDate.format('YYYY-MM-DD')
        const end = endDate.format('YYYY-MM-DD')
        const res = await this.client.get<CalendarEntryRaw[]>('accounts/me/crm-calendar', {
            params: {
                startDate: start,
                endDate: end,
            },
        })
        return res.data.map(
            entry =>
                ({
                    ...entry,
                    start: prepareDateTime(entry.start),
                    end: prepareDateTime(entry.end),
                    data: {
                        ...entry.data,
                        eventData: {
                            ...entry.data.eventData,
                            startTime: prepareDateTime(entry.start),
                            endTime: prepareDateTime(entry.end),
                        },
                    },
                } as CalendarEntry),
        )
    }

    getChallengeSet = async (id: string) => {
        const res = await this.client.get<any>(`challenge-sets/${id}`, {})
        return res.data
    }

    getGroups = async () => {
        const res = await this.client.get<{ groups: GroupRaw[]; participation: GroupRaw[] }>(
            'accounts/me/crm-groups',
            {},
        )
        const { groups, participation } = res.data
        return {
            groups: groups.map(
                group =>
                    ({
                        ...group,
                        lastDay: prepareDateTime(group.lastDay),
                        firstDay: prepareDateTime(group.firstDay),
                        schedules: group.schedules.map(sch => ({
                            ...sch,
                            startTime: prepareTime(sch.startTime),
                            endTime: prepareTime(sch.endTime),
                            startDate: prepareDateTime(sch.startDate),
                            endDate: prepareDateTime(sch.endDate),
                        })),
                    } as Group),
            ),
            participation: participation.map(
                group =>
                    ({
                        ...group,
                        lastDay: prepareDateTime(group.lastDay),
                        firstDay: prepareDateTime(group.firstDay),
                        schedules: group.schedules.map(sch => ({
                            ...sch,
                            startTime: prepareTime(sch.startTime),
                            endTime: prepareTime(sch.endTime),
                            startDate: prepareDateTime(sch.startDate),
                            endDate: prepareDateTime(sch.endDate),
                        })),
                    } as Group),
            ),
        }
    }

    getCourseType = async (id: string, publishedOnly: boolean): Promise<CourseType> => {
        const publishedParam = publishedOnly ? '?published=true' : ''
        const { data: courseType } = await this.client.get<{
            id: string
            name: string
            summary: string
            description: string
            subject: string
            language: string
            version: number
        }>(`/course-types/${id}${publishedParam}`)
        const { data: lessons } = await this.client.get<any>(`/course-types/${id}/lessons${publishedParam}`)
        const { data: tutorials } = await this.client.get<any>(`/course-types/${id}/tutorials${publishedParam}`)
        return { ...courseType, lessons, tutorials }
    }

    getLesson = async (courseTypeId: string, id: string, useCache: boolean): Promise<LessonAndTimesheet> => {
        const { data: lesson } = await this.client.get<any>(`/course-types/${courseTypeId}/lessons/${id}`)
        const endpoint = useCache ? 'data' : 'data-no-cache'
        const { data: timesheet } = await this.client.get<any>(
            `/files/${lesson.timesheet}/${endpoint}`,
            useCache ? { params: removeTokenFromParams() } : {},
        )
        return { lesson, timesheet }
    }

    getTimesheet = async (id: string): Promise<Timesheet> => {
        const { data: timesheet } = await this.client.get<any>(`/files/${id}/data-no-cache`)
        return timesheet
    }

    saveTimesheet = async (id: string, timesheet: Timesheet) => {
        var formData = new FormData()
        var blob = new Blob([JSON.stringify(timesheet)], { type: 'application/json' })
        formData.append('id', id)
        formData.append('metadata', JSON.stringify({ public: true }))
        formData.append('kind', 'document/timesheet')
        formData.append('file', blob)
        return this.client.post('/files/replace', formData)
    }

    getTimestampString = (startTime: Dayjs | undefined): string => {
        return dayjs(startTime).set('seconds', 0).set('milliseconds', 0).toISOString()
    }

    getAttendance = async (sessionId: string, timestamp: string, groupId?: string): Promise<Attendances> => {
        let part1 = `groupId=${groupId}`
        if (!groupId) {
            part1 = `type=makeup`
        } else {
            part1 += '&type=combined'
        }
        const { data } = await this.client.get(
            `/accounts/crm-attendance?${part1}&sessionId=${sessionId}&startDate=${timestamp}&endDate=${timestamp}`,
        )
        return data
    }

    postAttendances = async (items: any[]) => {
        const { data } = await this.client.post('/accounts/crm-attendance', items)
        return data
    }

    getCurriculum = async (publishedOnly: boolean) => {
        const publishedParam = publishedOnly ? '?published=true' : ''
        const [cu, su, co] = await Promise.all([
            this.client.get<any[]>(`/curricula${publishedParam}`),
            this.client.get<{ id: string; curriculum: string; courseTypes: any[] }[]>(`/subjects${publishedParam}`),
            this.client.get<({ id: string; name: string; summary: string; subject: string } | any)[]>(
                `/course-types${publishedParam}`,
            ),
        ])

        const curricula = cu.data
        const subjects = su.data.slice().sort(({ name: a }: any, { name: b }: any) => (a <= b ? -1 : 1))
        const courseTypes = co.data

        const processedSubjects: any[] = []

        const processedCuricula = curricula
            .map((curriculum: any) => ({
                ...curriculum,
                subjects: subjects
                    .reduce<any[]>((res, subject) => {
                        if (subject.curriculum === curriculum.id) {
                            const processedSubject = {
                                ...subject,
                                courseTypes: courseTypes.reduce<any[]>((res, courseType) => {
                                    const label = `${courseType.language}-${courseType.locale}${
                                        courseTypes.every(({ version: v }: any) => v === courseType.version)
                                            ? ''
                                            : ` | ${courseType.version}`
                                    }${!!courseType.edition ? ` | ${courseType.edition}` : ''}`
                                    return courseType.subject === subject.id
                                        ? [
                                              ...res,
                                              {
                                                  ...courseType,
                                                  label,
                                              },
                                          ]
                                        : [...res]
                                }, []),
                            }
                            processedSubjects.push(processedSubject)
                            return [...res, processedSubject]
                        }

                        return res
                    }, [])
                    .filter(s => s.courseTypes.length > 0),
            }))
            .filter(({ subjects }) => subjects.length > 0)
            .sort(({ name: a }, { name: b }) =>
                a.toLowerCase() <= b.toLowerCase() ? (a.toLowerCase() !== b.toLowerCase() ? -1 : 0) : 1,
            )

        return { curricula: processedCuricula, subjects: processedSubjects }
    }
}
