import { Reducer } from 'redux'
import {
    CurriculumReducerState,
    CurriculumAction,
    SET_CURRICULUM_FILTER_TEXT,
} from './curriculumTypes'

const INITIAL_STATE: CurriculumReducerState = {
    filterText: '',
}

export const curriculumReducer: Reducer<CurriculumReducerState, CurriculumAction> = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case SET_CURRICULUM_FILTER_TEXT:
            if (state.filterText === action.payload) return state
            return {
                ...state,
                filterText: action.payload,
            }

        default:
            return state
    }
}
