import React, { useEffect, useState } from 'react'
import { CardContent, Checkbox } from '@material-ui/core'
import { Box, Stack } from 'components/layout'
import { TimesheetEntryGroup } from '../../../types/groupTypes'
import { CardHeader } from 'components/card'
import { GroupEntryView } from './GroupEntryView'
import { useTranslate } from 'lib/i18n/useTranslate'
import { Accordion } from 'components/Accordion'
import styled from 'styled-components/macro'
import { useInClassContext } from '../InClassModeCard'

const HorizontalLeftAlignedStack = styled(Box)`
    display: flex;
    align-items: center;
    gap: ${props => props.theme.spacing(1)};
    justify-content: flex-start;
`

const EmbeddedAccordion = styled(Accordion)`
    .MuiAccordionSummary-root {
        padding: 0 1rem 0 0;
    }

    .MuiAccordionSummary-root:hover {
        background: ${props => props.theme.palette.action.hover};
    }

    .MuiAccordionSummary-content {
        margin: 0;
    }

    .MuiAccordionSummary-content.Mui-expanded {
        margin: 0.5rem 0;
    }

    .MuiAccordionSummary-expandIcon {
        color: ${props => props.theme.palette.text.secondary};
    }
`

const StyledCheckbox = styled(Checkbox)`
    margin-left: 1rem;
    margin-right: 0.5rem;
    color: ${props => props.theme.palette.primary.main};
`

export const EntryGroup: React.FC<{ entry: TimesheetEntryGroup; index: number; sessionId?: string }> = ({
    entry,
    index,
    sessionId,
}) => {
    const t = useTranslate()
    const [expanded, setExpanded] = useState(true)
    const { isInClassMode, isGroupCompleted, toggleGroup } = useInClassContext(index)

    const duration =
        entry.duration === 0
            ? ''
            : entry.duration > 1
            ? t('common.minutes.mynext', { duration: entry.duration, defaultValue: entry.duration + ' minutes' })
            : t('common.minute.mynext', { duration: entry.duration, defaultValue: '1 minute' })

    const entries = entry.content.entries
    const title =
        entries && entries[0] && entries[0].type === 'quiz'
            ? t('timesheet.quiz.title', { defaultValue: 'Quiz' })
            : entry.content.topic

    useEffect(() => {
        setExpanded(!isGroupCompleted)
    }, [isGroupCompleted])

    return (
        <EmbeddedAccordion
            expanded={expanded}
            onChange={() => setExpanded(!expanded)}
            style={{
                opacity: isInClassMode && isGroupCompleted && entry.isActive ? 0.7 : 1,
            }}
            header={
                <HorizontalLeftAlignedStack>
                    {isInClassMode && entry.isActive && (
                        <StyledCheckbox color="primary" checked={isGroupCompleted} onChange={toggleGroup} />
                    )}
                    <CardHeader title={title} subheader={entry.duration ? duration : undefined} />
                </HorizontalLeftAlignedStack>
            }
        >
            <CardContent style={{ padding: '0.5rem 0.7rem' }}>
                <Stack>
                    {entries?.map((entry, key) => (
                        <Box key={key} margin={1}>
                            <GroupEntryView
                                entry={entry}
                                nextEntry={entries[key + 1]}
                                groupIndex={index}
                                entryIndex={key}
                                sessionId={sessionId}
                            />
                        </Box>
                    ))}
                </Stack>
            </CardContent>
        </EmbeddedAccordion>
    )
}
