import React from 'react'
import { Text } from 'components/Text'
import { DownloadIcon, FileIcon, PrintIcon } from 'components/icons'

export const AttachmentKindIcon: React.FC<{ kind: string; print?: boolean }> = ({ kind, print }) => {
    switch (kind) {
        case 'document':
            return (
                <Text style={{ marginTop: '.2rem', marginRight: '.3rem' }}>
                    {!print && <FileIcon />}
                    {print && <PrintIcon />}
                </Text>
            )
        case 'document/zip':
            return (
                <Text style={{ marginTop: '.2rem', marginRight: '.3rem' }}>
                    <DownloadIcon />
                </Text>
            )

        default:
            return <Text style={{ marginRight: '.3rem' }}></Text>
    }
}
