import { Select, Option, SelectBox, SelectBoxItem } from 'components/form'
import { TextField } from 'components/form'
import React, { useEffect, useState } from 'react'
import { DiscoverProjectsFilterState } from './DiscoverProjects'
import styled from 'styled-components/macro'
import { CloseIcon, FavoriteIcon, FilterIcon, Searchicon, TrainerIcon } from 'components/icons'
import { InputAdornment } from 'components/form'
import { ApiTypes } from 'lib/api'
import { DecodedValueMap, SetQuery } from 'use-query-params'
import { useDebounce } from 'use-debounce/lib'
import { useTranslate } from 'lib/i18n/useTranslate'
import { Stack } from 'components/layout'
import { ProjectListTab } from '../ProjectListTab'
import { WrappingInline } from 'components/layout/WrappingInline'
import { Card, CardContent } from 'components/card'
import { useBlockGalleriesQuery } from '../projectQueries'
import { Collapse, Popover, Theme, useMediaQuery } from '@material-ui/core'
import { useHasGrant } from 'lib/grants'
import { IconButton } from 'components/IconButton'
import { Button } from 'components/Button'
import { ProjectLabelSelect } from '../ProjectLabelSelect'
import { Authorize } from 'components/Authorize'

interface DiscoverProjectsHeaderProps {
    filters: DecodedValueMap<DiscoverProjectsFilterState>
    setFilters: SetQuery<DiscoverProjectsFilterState>
    languages: ApiTypes['language'][]
    type: 'normal' | 'mini-quiz'
}

const AdvancedFilters: React.FC<Pick<DiscoverProjectsHeaderProps, 'filters' | 'setFilters'>> = ({ filters, setFilters }) => {
    const { data: blockGalleries } = useBlockGalleriesQuery()
    const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null)
    const t = useTranslate()
    const isMobile = useMediaQuery<Theme>(theme => theme.breakpoints.down('xs'))
    const hasGrant = useHasGrant()
    const isLanguageFilterEnabled =
        hasGrant({ product: 'edup.scoolcode.ide', feature: 'language-python' }) &&
        hasGrant({ product: 'edup.scoolcode.ide', feature: 'language-stagescript' }) &&
        filters.kind === 'mix'

    const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        setAnchorEl(event.currentTarget)
    }

    const handleClose = () => {
        setAnchorEl(null)
    }
    const isOpen = !!anchorEl
    const isFiltering = filters.kind !== '' || filters.language !== '' || filters.palette !== '' || (filters.labels && filters.labels.length > 0)
    const filterString = (() => {
        if (!isFiltering) {
            return t('general::filters')
        }
        let filterString = [
            capitalizeFirstLetter(filters.kind || ''),
            (filters.language === 'stagescript' ? 'StageScript' : filters.language === 'python' ? 'Python' : ''),
            ...(filters.labels ?? []),
            blockGalleries?.data.find(gallery => gallery.id === filters.palette)?.name,
        ]
            .filter(field => !!field)
            .join(', ')

        if (filterString.length > 50) {
            filterString = filterString.slice(0, 50) + ''
        }

        return filterString
    })()

    const filteredBlockGalleries = blockGalleries?.data
    .filter(gallery => {
        if (filters.kind === 'blox') {
            return !gallery.language || gallery.language === 'StageScript'
        }
        else if (filters.kind === 'mix') {
            if (!gallery.language || filters.language === 'stagescript') {
                return gallery.language === 'StageScript'
            }
            else if (filters.language === 'python') {
                return gallery.language === 'StagePython'
            }
        }
        return true;
    })
    .sort((a: any, b: any) => a.name.localeCompare(b.name))
    .map(gallery => (
        <Option key={gallery.id} value={gallery.id}>
            {gallery.name}
        </Option>
    ))

    return (
        <>
            {isMobile ? (
                <IconButton size="small" onClick={handleClick} color={isFiltering ? 'primary' : 'inherit'}>
                    <FilterIcon />
                </IconButton>
            ) : (
                <Button
                    onClick={handleClick}
                    color={isFiltering ? 'primary' : 'default'}
                    startIcon={<FilterIcon />}
                    variant="text"
                >
                    {filterString}
                </Button>
            )}

            <Popover PaperProps={{ style: { width: 360 } }} anchorEl={anchorEl} open={isOpen} onClose={handleClose}>
                <Stack p={4}>
                    <SelectBox
                        label={t('projects::type')}
                        value={filters.kind}
                        onChange={value => {
                            switch (value) {
                                case 'blox':
                                case '':
                                    setFilters({ kind: value, language: '', palette: '' }, 'replaceIn')
                                    break;
                                case 'mix':
                                    setFilters({ kind: value, palette: '' }, 'replaceIn')
                                    break
                                default:
                                    break;
                            }
                        }}
                    >
                        <SelectBoxItem fullWidth value="blox">
                            Blox
                        </SelectBoxItem>
                        <SelectBoxItem fullWidth value="mix">
                            Mix
                        </SelectBoxItem>
                        <SelectBoxItem fullWidth value="">
                            {t('general::all')}
                        </SelectBoxItem>
                    </SelectBox>
                    {isLanguageFilterEnabled && <SelectBox
                        label={t('projects::language::label')}
                        value={filters.language}
                        onChange={value => {
                            setFilters({ language: value as any, palette: '' }, 'replaceIn')
                        }}
                    >
                        <SelectBoxItem value="stagescript">
                            StageScript
                        </SelectBoxItem>
                        <SelectBoxItem fullWidth value="python">
                            Python
                        </SelectBoxItem>
                        <SelectBoxItem fullWidth value="">
                            {t('general::all')}
                        </SelectBoxItem>
                    </SelectBox>}
                    {hasGrant('project-labels') && (
                        <ProjectLabelSelect
                            variant="filter"
                            onChange={value => {
                                setFilters({ labels: value }, 'replaceIn')
                            }}
                            value={filters.labels}
                        />
                    )}
                    <Select
                        onChange={e => {
                            setFilters({ palette: e.target.value as string }, 'replaceIn')
                        }}
                        displayEmpty
                        value={filters.palette}
                        label={t('projects::palette')}
                    >
                        <OptionAll value="">
                            <em>{t('general::all')}</em>
                        </OptionAll>
                        {filteredBlockGalleries}
                    </Select>
                    <Collapse in={isFiltering}>
                        <Button
                            onClick={() => {
                                setFilters({ kind: '', palette: '', language: '', labels: [] }, 'replaceIn')
                            }}
                            startIcon={<CloseIcon />}
                            variant="text"
                        >
                            {t('general::resetFilters')}
                        </Button>
                    </Collapse>
                </Stack>
            </Popover>
        </>
    )
}

export const DiscoverProjectsHeader: React.FC<DiscoverProjectsHeaderProps> = ({ filters, setFilters, languages, type }) => {
    const t = useTranslate()

    return (
        <Stack spacing={5}>
            <WrappingInline>
                <ProjectListTab
                    isActive={filters.view === 'all'}
                    onClick={() => {
                        setFilters({ view: 'all' }, 'replaceIn')
                    }}
                >
                    {t('general::all')}
                </ProjectListTab>
                <ProjectListTab
                    isActive={filters.view === 'favorites'}
                    onClick={() => {
                        setFilters({ view: 'favorites' }, 'replaceIn')
                    }}
                    icon={<FavoriteIcon />}
                >
                    {t('general::favorites')}
                </ProjectListTab>
                <ProjectListTab
                    isActive={filters.view === 'only-trainers'}
                    onClick={() => {
                        setFilters({ view: 'only-trainers' }, 'replaceIn')
                    }}
                    icon={<TrainerIcon />}
                >
                    {t('general::instructor')}
                </ProjectListTab>
            </WrappingInline>
            <Card elevation={1}>
                <CardContent>
                    <Stack spacing={6}>
                        <Grid>
                            <Search filters={filters} setFilters={setFilters} />
                            <InnerGrid>
                                {type === 'normal' && <Authorize grant={{ product: 'edup.universe', feature: 'advanced-palette-filtering' }}>
                                    <AdvancedFilters filters={filters} setFilters={setFilters}/>
                                </Authorize>}
                                <LanguageSelect
                                    fullWidth={false}
                                    multiple
                                    value={filters.languages}
                                    onChange={e => {
                                        const selectedLanguages = e.target.value as string[]
                                        if (selectedLanguages.length === 0) {
                                            e.preventDefault()
                                        }
                                        else {
                                            setFilters({ languages: selectedLanguages }, 'replaceIn')
                                        }
                                    }}
                                >
                                    {languages.map(language => (
                                        <Option value={language.code} key={language.code}>
                                            {language.displayName}
                                        </Option>
                                    ))}
                                </LanguageSelect>
                                {filters.sort === '-daysViewedCount' && (
                                    <Select
                                        fullWidth={false}
                                        value={filters.days}
                                        onChange={e => {
                                            setFilters({ days: e.target.value as number }, 'replaceIn')
                                        }}
                                    >
                                        <Option value={1}>{t('timeRanges::day')}</Option>
                                        <Option value={7}>{t('timeRanges::week')}</Option>
                                        <Option value={30}>{t('timeRanges::month')}</Option>
                                        <Option value={-1}>{t('timeRanges::all')}</Option>
                                    </Select>
                                )}
                                <Select
                                    fullWidth={false}
                                    value={filters.sort}
                                    onChange={e => {
                                        setFilters({ sort: e.target.value as string }, 'replaceIn')
                                    }}
                                >
                                    <Option value="-approvedAt">{t('projects::sort::latest')}</Option>
                                    <Option value="-cookieCount">{t('projects::sort::mostCookies')}</Option>
                                    <Option value="-goldenCookieCount">{t('projects::sort::mostGoldenCookies')}</Option>
                                    <Option value="-daysViewedCount">{t('projects::sort::mostViews')}</Option>
                                </Select>
                            </InnerGrid>
                        </Grid>
                    </Stack>
                </CardContent>
            </Card>
        </Stack>
    )
}

const Search: React.FC<Omit<DiscoverProjectsHeaderProps, 'languages' | 'type'>> = ({ filters, setFilters }) => {
    const [search, setSearch] = useState(filters.pattern)
    const [debouncedSearch] = useDebounce(search, 500)
    const t = useTranslate()

    useEffect(() => {
        setSearch(filters.pattern)
    }, [filters.pattern])

    useEffect(() => {
        setFilters({ pattern: debouncedSearch }, 'replaceIn')
    }, [debouncedSearch, setFilters])

    return (
        <TextField
            placeholder={t('projects::discoverSearchPlaceholder')}
            type="search"
            startAdornment={
                <InputAdornment position="start">
                    <Searchicon />
                </InputAdornment>
            }
            value={search}
            onChange={e => {
                setSearch(e.target.value)
            }}
        />
    )
}

const Grid = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: ${props => props.theme.spacing(2)};
    ${props => props.theme.breakpoints.down('sm')} {
        grid-template-columns: 1fr;
    }
`

const InnerGrid = styled.div`
    display: flex;
    justify-content: flex-end;
    gap: ${props => props.theme.spacing(2)};
    ${props => props.theme.breakpoints.down('sm')} {
        justify-content: flex-start;
        flex-wrap: wrap;
        > * {
            flex: 1;
        }
    }
    ${props => props.theme.breakpoints.down('xs')} {
        display: grid;
        grid-template-columns: 1fr;
        > * {
            flex: 1;
        }
    }
`

const LanguageSelect = styled(Select)`
    width: 12rem;
    ${props => props.theme.breakpoints.down('sm')} {
        width: auto;
    }
`

const OptionAll = styled(Option)`
    font-weight: 600;
`

function capitalizeFirstLetter(string: string) {
    return string.charAt(0).toUpperCase() + string.slice(1)
}
