import React from 'react'
import { Stack } from 'components/layout'
import { TimesheetTextGroupEntry, TimesheetGroupEntry } from '../../../types/groupTypes'
import { HTMLText } from './HTMLText'
import { useTranslate } from 'lib/i18n/useTranslate'
import { Button } from 'components/Button'
import { Divider } from 'components/Divider'

export const SlideCount: React.FC<{ entry: TimesheetGroupEntry }> = ({ entry: { slide_count = 0 } }) => {
    const t = useTranslate();

    if(!slide_count) return null;
    return (
        <Button variant="outlined" disabled>
            {(slide_count > 1) ? t('timesheet::slides', { count: slide_count }) : t('timesheet.slide')}
        </Button>
    )
}

export const TextGroupEntry: React.FC<{ entry: TimesheetTextGroupEntry, nextEntry?: TimesheetGroupEntry }> = ({ entry, nextEntry }) => {
    return (
        <Stack>
            <HTMLText text={entry.content} />
            {/*<SlideCount entry={entry} />*/}
            {nextEntry?.type === "text" && <Divider />}
        </Stack>
    )
}
