import React from 'react'
import { Card, CardContent } from 'components/card'
import { useTranslate } from 'lib/i18n/useTranslate'
import { Button } from 'components/Button'
import { Stack } from 'components/layout'
import { FormControl, FormLabel, TextField } from 'components/form'
import { Text } from 'components/Text'
import { api } from 'lib/api'
import { useUserState } from 'contexts/UserContext'
import MoodRating from './elements/MoodRating'

export interface FeedbackCardProps {
    timesheet: string
    lessonType: string
    lessonIndex: number
    courseType: string
    numberOfStudents: number
}

export const FeedbackCard: React.FC<FeedbackCardProps> = props => {
    const t = useTranslate()
    const user = useUserState()
    const [status, setStatus] = React.useState<'idle' | 'loading' | 'success' | 'error'>('idle')
    const [ratingValue, setRatingValue] = React.useState<number | null>(null)
    const [commentValue, setCommentValue] = React.useState<string | null>(null)
    const isFilled = ratingValue !== null && commentValue !== null && commentValue.length >= 6

    async function submitFeedback() {
        setStatus('loading')
        try {
            await api.groups.submitTimesheetFeedback({
                school: user.crm.lastSchoolId,
                timesheet: props.timesheet,
                studentRatings: 0,
                teachersRating: ratingValue,
                comment: commentValue,
                lessonLength: 0,
                lessonType: props.lessonType,
                lessonIndex: props.lessonIndex,
                courseType: props.courseType,
                progress: 0,
                endClass: false,
                numberOfStudents: props.numberOfStudents,
                status: 'waiting',
            })
            setStatus('success')
        } catch (e) {
            console.error(e)
            setStatus('error')
            return
        }
    }

    function reset() {
        setRatingValue(null)
        setCommentValue(null)
        setStatus('idle')
    }

    if (status === 'success') {
        return (
            <Card>
                <CardContent>
                    <Stack spacing={2}>
                        <Text>{t('timesheet.feedbackModal.thanks')}</Text>
                        <Text variant="body2">{t('timesheet.feedbackModal.anythingElse')}</Text>
                        <Button onClick={reset}>{t('timesheet.feedbackModal.newFeedback')}</Button>
                    </Stack>
                </CardContent>
            </Card>
        )
    }

    return (
        <Card>
            <CardContent>
                <Stack spacing={2}>
                    <FormControl>
                        <FormLabel>{t('timesheet.feedbackModal.details')}</FormLabel>
                        <MoodRating value={ratingValue} name="teachers-rating" onChange={(_, v) => setRatingValue(v)} />
                    </FormControl>
                    <TextField
                        readOnly={status === 'loading'}
                        label={t(`timesheet.feedbackModal.comments`)}
                        multiline
                        name="feedback-comments"
                        rows={3}
                        value={commentValue || ''}
                        inputProps={{ minLength: 6, maxLength: 500 }}
                        onChange={e => {
                            setCommentValue(e.target.value)
                        }}
                    ></TextField>
                    <Button isLoading={status === 'loading'} disabled={!isFilled} onClick={submitFeedback}>
                        {t('timesheet.feedbackModal.submit')}
                    </Button>
                </Stack>
            </CardContent>
        </Card>
    )
}
